import React from 'react';
import Tooltip from 'react-tooltip-lite';

import RadioButton from './../../../../components/RadioButton';
import Slider from './../../../../components/Slider';
import Editor from './../../../../components/Editor';
import LoaderEditor from './../../../../components/Loaders/LoaderEditor';
import LoaderCheckboxList from './../../../../components/Loaders/LoaderCheckboxList';
import LoaderSlider from './../../../../components/Loaders/LoaderSlider';
import Lightbulb from './../../../../components/Lightbulb';
import TitleQuestionair from './../../../../components/Atoms/Title-Questionair';

function MijnWerkleven2Pagina2(props) {
  return (
    <React.Fragment>
      <TitleQuestionair>
        B. Gewenst werkleven
        <Tooltip
          content={
            <div className="max-w-sm p-4">
              <p>
                Het doel is dat jij weet wat jij wilt in jouw gewenste
                werkleven: jouw eisen en wensen.
              </p>
              <ul className="list-disc pl-2 pt-2">
                <li>
                  Je start met een lijstje maken van jouw eisen. Eisen
                  zijn punten waaraan jouw werk zeker moet voldoen
                  (aantal uren, salaris, reisafstand, etc.).
                </li>
                <li>
                  Dan maak je een lijstje van jouw wensen. Wensen
                  kunnen van alles zijn: van iets wat je nu al doet
                  tot aan je leukste dromen, wensen, verlangens,
                  ideeën en ambities. Waarschijnlijk komen er al
                  allemaal gedachten in je hoofd die onder eisen of
                  wensen vallen.
                </li>
                <li>
                  In de lijstjes kan je punten toevoegen, aanpassen,
                  slepen of verwijderen.{' '}
                </li>
                <li>
                  Elke eis/wens ga je daarna verwerken. Je kunt zelf
                  kiezen of je dit gelijk doet of op een later moment.
                </li>
                <li>
                  Hoe beter je elk punt verwerkt, hoe completer de
                  samenvatting is op jouw dashboard.
                </li>
              </ul>
            </div>
          }
          className="inline-block z-20"
        >
          <Lightbulb />
        </Tooltip>
      </TitleQuestionair>


      <h3 className="font-bold text-gray-700 mb-2">
        Stel je eens voor dat ik een cijfer kan geven aan mijn
        gewenste werkleven. Welk cijfer is dat?
      </h3>
      {props.loading === false ? (
        <Slider
          sliderValue={props.GWSliderValue}
          onChange={props.handleSliderChange}
          keyName="GWCijfer"
        />
      ) : (
        <LoaderSlider />
      )}
      <div className="mt-5">
        <h3 className="font-bold text-gray-700 mb-2">
          Waarom geef ik dat cijfer?
        </h3>
        {props.loading === false ? (
          <Editor
            setEditorState={props.setEditorState}
            fieldName="GWToelichting"
            initialEditorState={props.initialEditorStateGWToelichting}
          />
        ) : (
          <LoaderEditor />
        )}
      </div>
      <div className="mt-5">
        <h3 className="font-bold text-gray-700 mb-2">
          Hoe zou ik mij dan voelen? Kies een smiley.
        </h3>
        {props.loading === false ? (
          <form
            className="mt-6 text-2xl"
            onChange={e => props.handleChange(e)}
          >
            <RadioButton
              labelValue="😭"
              checked={props.GWSmiley === '1'}
              value="1"
              name="GWSmiley"
            />
            <RadioButton
              labelValue="😔"
              checked={props.GWSmiley === '2'}
              value="2"
              name="GWSmiley"
            />
            <RadioButton
              labelValue="😐"
              checked={props.GWSmiley === '3'}
              value="3"
              name="GWSmiley"
            />
            <RadioButton
              labelValue="🙂"
              checked={props.GWSmiley === '4'}
              value="4"
              name="GWSmiley"
            />
            <RadioButton
              labelValue="😄"
              checked={props.GWSmiley === '5'}
              value="5"
              name="GWSmiley"
            />
          </form>
        ) : (
          <LoaderCheckboxList />
        )}
      </div>
      <div className="mt-8">
        <h3 className="font-bold text-gray-700 mb-2">
          Hoe is dat dan aan mij te zien of te merken?
          <Tooltip
            content={
              <div className="max-w-sm">
                <div>
                  <ul>
                    <li>Ben je vrolijker? </li>
                    <li>Voel je je meer ontspannen?</li>
                    <li>Heb je meer energie?</li>
                    <li>Ben je productiever? </li>
                    <li>Ben je meer gemotiveerd?</li>
                    <li>Loop je weer fluitend rond?</li>
                    <li>Ben je weer helemaal aan het shinen?</li>
                    <li>Stap je makkelijker met je goede been uit bed? </li>
                  </ul>
                </div>
              </div>
            }
            className="inline-block z-20"
          >
            <Lightbulb />
          </Tooltip>
        </h3>
        {props.loading === false ? (
          <Editor
            setEditorState={props.setEditorState}
            fieldName="GWGevoel"
            initialEditorState={props.initialEditorStateGWGevoel}
          />
        ) : (
          <LoaderEditor />
        )}
      </div>
    </React.Fragment>
  );
}

export default MijnWerkleven2Pagina2;
