import React from 'react';
import ContentLoader from 'react-content-loader';

const LoaderTitle = () => (
  <ContentLoader
    height={5}
    width={25}
    className="inline-block w-48 h-5 relative -mt-1"
  >
    <rect x="0" y="0" rx="1" ry="1" width="25" height="5" />
  </ContentLoader>
);

export default LoaderTitle;
