import React, { Component } from 'react';
import { withFirebase } from './../Firebase';
import CreatableSelect from 'react-select/creatable';
import Select from 'react-select';

import uuidv1 from 'uuid/v1';

class SelectLabel extends Component {
  constructor(props) {
    super(props);

    this.state = {
      labels: [],
      selectedLabels: [],
      authUser: '',
    };

    this.handleChange = this.handleChange.bind(this);
    this.makeLabelsArray = this.makeLabelsArray.bind(this);
    this.saveNewLabelToDatabase = this.saveNewLabelToDatabase.bind(
      this,
    );
  }

  handleChange = (newValue, actionMeta) => {
    console.log('new value', newValue);
    this.setState({
      selectedLabels: newValue,
    });

    // Index wordt gebruikt bij de actielijst
    if (this.props.index !== undefined) {
      this.props.handleLabelChange(
        newValue,
        this.props.index,
        this.props.item,
      );
    } else {
      this.props.handleLabelChange(newValue);
    }
  };

  makeLabelSlug(item) {
    /* eslint-disable */
    return item
      .toString()
      .trim()
      .toLowerCase()
      .replace(/\s+/g, '-')
      .replace(/[^\w\-]+/g, '')
      .replace(/\-\-+/g, '-')
      .replace(/^-+/, '')
      .replace(/-+$/, '');
  }

  saveNewLabelToDatabase(nieuwLabel, generatedUUID) {
    console.log('save goal to DB');
    const firebase = this.props.firebase;

    const newLabelObject = {
      UUID: generatedUUID,
      label: nieuwLabel,
      value: nieuwLabel,
      labelNaam: nieuwLabel,
      labelSlug: this.makeLabelSlug(nieuwLabel),
    };

    const docRef = firebase.db
      .collection('users')
      .doc(this.state.authUser)
      .collection('actielijst')
      .doc('labels');

    docRef
      .update({
        currentLabels: firebase.fieldValue.arrayUnion(newLabelObject),
      })
      .then(function() {
        console.log('Doel successfully written to db!');
      })
      .catch(function(error) {
        console.error('Error writing document: ', error);
      });
  }

  /*
    <CreatableSelect
      name="labels"
      isMulti
      className=""
      placeholder={'Kies..!'}
      onChange={this.handleChange}
      options={this.state.labels}
      value={this.state.selectedLabels}
      noOptionsMessage={() => {
        return 'Nog geen doel, maak die later aan!';
      }}
      formatCreateLabel={(inputValue) => {
        return `Nieuw Doel: ${inputValue}`;
      }}
    />
 */

  render() {
    return (
      <Select
        options={this.state.labels}
        isSearchable={false}
        onChange={this.handleChange}
        value={this.state.selectedLabels}
      />
    );
  }

  makeLabelsArray(array) {
    let labelsArray = [];
    if (array !== undefined && array.length > 0) {
      array.forEach((item, index) => {
        console.log('ITEM!');
        console.log(item);
        let labelObject = {
          value: item.value,
          label: item.label,
          UUID: item.UUID,
        };
        labelsArray.push(labelObject);
      });
    }
    return labelsArray;
  }

  componentDidMount() {
    console.log(this.props.labels);
    console.log('items', this.props.item);
    console.log('this.props.standardLabel', this.props.standardLabel);
    // this.props.labels = Bestaande labels vanuit de actielijst. Deze zijn altijd het meest actueel.
    // this.props.standardLabel = Bestaande labels die in het actie item object zitten opgeslagen. Als er vanuit de actielijst een label wordt verwijderd blijven deze in de acties zitten. Deze worden er hier uitgefilterd. Ook worden altijd de actuele namen toegewezen op basis van de waarden meegekregen uit this.props.labels.
    let standardLabels = this.props.standardLabel;
    if (this.props.standardLabel) {
      console.log(this.props.standardLabel);
      let labelsArray = [];
      if (
        this.props.standardLabel !== undefined &&
        this.props.standardLabel.length > 0
      ) {
        this.props.standardLabel.forEach((actieLabel, index) => {
          console.log('actieLabel!');
          console.log(actieLabel);

          // Kijk of we dit label in de gekregen labels kunnen vinden
          const labelBestaatNog = this.props.labels.find(obj => {
            return obj.UUID === actieLabel.UUID;
          });

          console.log(labelBestaatNog);
          if (labelBestaatNog === undefined) {
            // Label bestaat niet meer
            return;
          }

          let labelObject = {
            value: labelBestaatNog.labelNaam,
            label: labelBestaatNog.labelNaam,
            UUID: actieLabel.UUID,
          };

          labelsArray.push(labelObject);
        });
        let label = labelsArray.length > 0 ? labelsArray[0] : [];
        this.setState(
          {
            selectedLabels: label,
          },
          () => console.log(this.state),
        );
      } else {
        this.setState({
          selectedLabels: [],
        });
      }
    }

    // Get alle labels
    let firebase = this.props.firebase;
    let that = this;

    firebase.auth.onAuthStateChanged(function(user) {
      if (user) {
        that.setState({
          authUser: user.uid,
        });

        var docRef = firebase.db
          .collection('users')
          .doc(user.uid)
          .collection('actielijst')
          .doc('labels');

        docRef
          .get()
          .then(function(doc) {
            if (doc.exists) {
              let initialState = [];
              console.log(doc.data().currentLabels);
              // .currentLabels bevat alle labels
              doc.data().currentLabels.forEach(item =>
                initialState.push({
                  value: item.labelNaam,
                  label: item.labelNaam,
                  UUID: item.UUID,
                }),
              );
              that.setState({
                labels: initialState,
                authUser: user.uid,
              });
            } else {
              docRef.set({
                labels: [],
              });
              that.setState({
                authUser: user.uid,
              });
            }
          })
          .catch(function(error) {
            console.log('Error getting document:', error);
          });
      }
    });
  }
}

export default withFirebase(SelectLabel);
