import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Home } from 'react-feather';
import ButtonSmallRed from "../Atoms/Button-Small-Red";

class DashboardHeading extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className="flex justify-between items-center">
        <h1 className="font-bold text-xl md:text-2xl text-black">
          Dashboard Workappic
        </h1>
        <div className="flex">
          <ButtonSmallRed title="Uitleg WorkAppic" onClick={this.props.toggleDashboardPopUp} />
        </div>
      </div>
    );
  }
}

DashboardHeading.propTypes = {
  toggleDashboardPopUp: PropTypes.func.isRequired,
  toggleAnalysePopUp: PropTypes.func.isRequired,
};

export default DashboardHeading;
