import React, { Component } from 'react';
import { withRouter, NavLink } from 'react-router-dom';
import PropTypes from 'prop-types';

import format from 'date-fns/format';
import isToday from 'date-fns/is_today';
import isAfter from 'date-fns/is_after';
import isBefore from 'date-fns/is_before';
import addDays from 'date-fns/add_days';
import nlLocale from 'date-fns/locale/nl';
import { makeDateObj } from '../../functions/utils';

class DashboardToDoLijstCountedLinks extends Component {
  constructor(props) {
    super(props);
    console.log('this.props.acties', this.props.acties);
  }

  arrayFilterGeselecteerdeLabels(array) {
    if (this.state.activeLabel === undefined) {
      return [];
    }

    const newArray = array.filter((element, index) => {
      if (element.labels !== undefined) {
        return element.labels.includes(
          this.state.activeLabel.labelNaam,
        );
      }
    });
    return newArray;
  }

  arrayFilterSpecifiekLabel(array) {
    const newArray = array.filter((element, index) => {
      return element.voltooid !== true;
    });
    return newArray;
  }

  arrayFilterVerwijderVoltooideActies(array) {
    const newArray = array.filter((element, index) => {
      return element.voltooid !== true;
    });
    return newArray;
  }

  arrayFilterVerwijderOnvoltooideActies(array) {
    const newArray = array.filter((element, index) => {
      return element.voltooid === true;
    });
    return newArray;
  }

  arrayFilterVerwijderHerinneringEnDeadline(array) {
    const newArray = array.filter((element, index) => {
      if (
        element.deadline === undefined &&
        element.herinneringSavedState === undefined
      ) {
        return element;
      }
      return null;
    });
    return newArray;
  }

  arrayFilterVerwijderZonderHerinneringEnDeadline(array) {
    const newArray = array.filter((element, index) => {
      if (element.deadline !== undefined) {
        return element;
      }
      return null;
    });
    return newArray;
  }

  arrayFilterVerwijderMetHerinneringEnDeadline(array) {
    const newArray = array.filter((element, index) => {
      if (element.deadlineSavedState === undefined) {
        return element;
      }
      return null;
    });
    return newArray;
  }

  arrayFilterDeadlineVandaag(array) {
    const newArray = array.filter((element, index) => {
      if (
        isToday(element.deadline) ||
        isBefore(element.deadline, new Date())
      ) {
        return element;
      }
      return null;
    });
    return newArray;
  }

  startOfWeek(date) {
    var diff =
      date.getDate() - date.getDay() + (date.getDay() === 0 ? -6 : 1);
    return new Date(date.setDate(diff)).setHours(0, 0, 0, 0);
  }

  arrayFilterDeadlineWeek(array) {
    const newArray = array.filter((element, index) => {
      if (
        isBefore(element.deadline, addDays(new Date(), 8)) &&
        isAfter(
          makeDateObj(element.deadline),
          this.startOfWeek(new Date()),
        )
      ) {
        return element;
      }
      return null;
    });
    return newArray;
  }

  getMindboxAmount(array) {
    let newArray = this.arrayFilterVerwijderVoltooideActies(array);
    newArray = this.arrayFilterVerwijderHerinneringEnDeadline(
      newArray,
    );
    return newArray.length;
  }

  getAlleActiesAmount(array) {
    let newArray = this.arrayFilterVerwijderVoltooideActies(array);
    newArray = this.arrayFilterVerwijderZonderHerinneringEnDeadline(
      array,
    );
    return newArray.length;
  }

  getVandaagAmount(array) {
    let newArray = this.arrayFilterVerwijderVoltooideActies(array);
    newArray = this.arrayFilterDeadlineVandaag(newArray);
    return newArray.length;
  }

  getWeekAmount(array) {
    let newArray = this.arrayFilterVerwijderVoltooideActies(array);
    newArray = this.arrayFilterDeadlineWeek(newArray);
    return newArray.length;
  }

  getVoltooidAmount(array) {
    let newArray = this.arrayFilterVerwijderOnvoltooideActies(array);
    return newArray.length;
  }

  componentDidMount() {
    console.log(this.props.acties);
  }

  render() {
    let mindboxAmount = 0;
    let alleActiesAmount = 0;
    let vandaagAmount = 0;
    let weekAmount = 0;
    let voltooidAmount = 0;
    console.log('in render', this.props.acties);
    if (this.props.acties?.length > 0) {
      mindboxAmount = this.getMindboxAmount(this.props.acties);
      alleActiesAmount = this.getAlleActiesAmount(this.props.acties);
      vandaagAmount = this.getVandaagAmount(this.props.acties);
      weekAmount = this.getWeekAmount(this.props.acties);
      voltooidAmount = this.getVoltooidAmount(this.props.acties);
    }

    return (
      <div className="p-0 md:w-64 mx-auto">
        <h3 className="font-bold text-lg text-gray-700 mb-1 mt-4">
          Actielijst
        </h3>
        <ul className="mt-2">
          <li className="text-gray-700 mb-2 font-bold font-xl">
            <NavLink
              activeClassName="is-active cursor-default font-bold text-gray-900"
              className="transition-fast hover:translate-t-2px hover:text-gray-900 text-gray-600 font-medium"
              to="/to-do-lijst"
              exact
            >
              Inbox
              <span className="count ml-2 text-sm text-gray-600">
                ({mindboxAmount !== undefined ? mindboxAmount : 0})
              </span>
            </NavLink>
          </li>
          <li className="text-gray-700 mb-2 font-xl">
            <NavLink
              activeClassName="is-active cursor-default font-bold text-gray-900"
              className="transition-fast hover:translate-t-2px hover:text-gray-900 text-gray-600 font-medium"
              to="/to-do-lijst/alle-acties"
              exact
            >
              Acties met datum
              <span className="count ml-2 text-sm text-gray-600">
                (
                {alleActiesAmount !== undefined
                  ? alleActiesAmount
                  : 0}
                )
              </span>
            </NavLink>
          </li>
          <li className="text-gray-700 mb-2 font-xl">
            <NavLink
              activeClassName="is-active cursor-default font-bold text-gray-900"
              className="transition-fast hover:translate-t-2px hover:text-gray-900 text-gray-600 font-medium"
              to="/to-do-lijst/vandaag"
              exact
            >
              Vandaag
              <span className="count ml-2 text-sm text-gray-600">
                ({vandaagAmount !== undefined ? vandaagAmount : 0})
              </span>
            </NavLink>
          </li>
          <li className="text-gray-700 mb-2 font-xl">
            <NavLink
              activeClassName="is-active cursor-default font-bold text-gray-900"
              className="transition-fast hover:translate-t-2px hover:text-gray-900 text-gray-600 font-medium"
              to="/to-do-lijst/deze-week"
              exact
            >
              Deze week
              <span className="count ml-2 text-sm text-gray-600">
                ({weekAmount !== undefined ? weekAmount : 0})
              </span>
            </NavLink>
          </li>
          <li className="text-gray-700 mb-2 font-xl">
            <NavLink
              activeClassName="is-active cursor-default font-bold text-gray-900"
              className="transition-fast hover:translate-t-2px hover:text-gray-900 text-gray-600 font-medium"
              to="/to-do-lijst/voltooid"
              exact
            >
              Voltooid
              <span className="count ml-2 text-sm text-gray-600">
                ({voltooidAmount !== undefined ? voltooidAmount : 0})
              </span>
            </NavLink>
          </li>
        </ul>
      </div>
    );
  }
}

export default DashboardToDoLijstCountedLinks;
