import React, { useState } from 'react';

import { goalStyling } from '../../functions/utils';
import { withFirebase } from '../../components/Firebase';
import { withAuthentication } from '../../components/Session';
import TimelineNode from './TimelineNode';

import { Flag } from 'react-feather';

const TimelineGoalNode = props => {
  return (
    <>
      {props.setOfMonths[0] ? (
        <div
          key={props.setOfMonths[0].labelNaam + '_' + props.index}
          style={{
            gridColumn: props.column,
            gridRowStart: props.zoomedIn
              ? 1
              : props.setOfMonths[0].rowPosition,
          }}
        >
          {props.setOfMonths.map((goal, index) => {
            return (
              <TimelineNode
                goal={goal}
                goals={props.goals}
                index={index}
                setLoading={props.setLoading}
                isDashboard={props.isDashboard}
              />
            );
          })}
        </div>
      ) : (
        <h3>
          Als je geen doelen hebt, hoe weet je dan waar je heen gaat?
        </h3>
      )}
    </>
  );
};

export default withAuthentication(withFirebase(TimelineGoalNode));
