const getDocRef = (firebase, authUser) => {
  return firebase.db.collection('users').doc(authUser.uid);
};

export const getNotificationSettings = (firebase, user) => {
  console.log('firebase', firebase, user);
  const docRef = firebase.db.collection('users').doc(user.uid);
  let notifications = false;

  return docRef
    .get()
    .then(function(doc) {
      if (doc.data()) {
        console.log('46 notifications', doc.data());
        notifications =
          doc.data().notifications === undefined
            ? true
            : doc.data().notifications;
        console.log('notifications', notifications);
      }
    })
    .then(function(doc) {
      return docRef
        .set(
          {
            notifications: notifications,
          },
          { merge: true },
        )
        .then(function() {
          console.log('Notification set to account', notifications);
          return notifications;
        })
        .catch(function(error) {
          console.log('Error getting document:', error);
        });
    });
};

export const setNotificationSettings = (firebase, user, value) => {
  console.log('setNotificationSettings', value);
  const docRef = firebase.db.collection('users').doc(user.uid);

  return docRef
    .set(
      {
        notifications: value,
      },
      { merge: true },
    )
    .then(function() {
      console.log('Notification set to account', value);
    })
    .catch(function(error) {
      console.log('Error getting document:', error);
    });
};
