import React, { Component } from 'react';
import {
  ResponsiveContainer,
  ComposedChart,
  Line,
  Area,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  LabelList,
  Tooltip,
  Legend,
} from 'recharts';

let renderLabel = function(entry) {
  return entry.name;
};

class DashboardHuidigWerklevenHorizontalBarChart extends Component {
  render() {
    console.log('this.props.aantalMinderLeukePuntenTeVerbeteren');
    console.log(this.props.aantalMinderLeukePuntenTeVerbeteren);
    const data = [
      {
        name: 'Minder Leuk',
        minderLeuk: this.props.aantalMinderLeukePunten,
        aantalMinderLeukePuntenTeVerbeteren: this.props
          .aantalMinderLeukePuntenTeVerbeteren,
      },
      {
        name: 'Leuk',
        leuk: this.props.aantalLeukePunten,
      },
    ];
    return (
      <ResponsiveContainer height={175} width="100%">
        <ComposedChart
          layout="vertical"
          data={data}
          margin={{
            top: 20,
            right: 20,
            bottom: 20,
            left: 40,
          }}
        >
          <XAxis type="number" hide={true} />
          <YAxis dataKey="name" type="category" />

          <Bar
            dataKey="minderLeuk"
            barSize={40}
            // stackId="a"
            fill="#F56565"
            label={{ fill: 'white' }}
            isAnimationActive={false}
          >
            {/* <LabelList dataKey="minderLeuk" fill="white" /> */}
          </Bar>

          <Bar
            dataKey="aantalMinderLeukePuntenTeVerbeteren"
            barSize={40}
            // stackId="a"
            fill="#ECC94B"
            label={{ fill: 'white' }}
            isAnimationActive={false}
          >
            {/* <LabelList
              dataKey="aantalMinderLeukePuntenTeVerbeteren"
              fill="white"
            /> */}
          </Bar>

          <Bar
            dataKey="leuk"
            barSize={40}
            fill="#38a169"
            stackId="b"
            label={{ fill: 'white' }}
            isAnimationActive={false}
          >
            <LabelList dataKey="leuk" fill="white" />
          </Bar>
        </ComposedChart>
      </ResponsiveContainer>
    );
  }
}

export default DashboardHuidigWerklevenHorizontalBarChart;
