import React from 'react';
import {Edit, Move, Trash2} from "react-feather";

const SortableTodoItem = (props) => {
    return (
        <li className="draggable-list-item cursor-move text-gray-700 z-20 text-lg border-b px-4 py-2 pb-1 relative shadow px-4 py-2 mb-2 relative border rounded-full wa-list-container-bg">
            {props.item.editing === false ? (
                <Move className="text-gray-400 inline-block mr-4 -mt-2 relative" />
            ) : null}
            {props.item.editing === false ? (
                <span className="select-none list-item-text inline-block relative">
          {props.listPosition + 1}. {props.item.title}
        </span>
            ) : (
                <input
                    type="text"
                    autoComplete="off"
                    className="edit-array-item-title-input mr-4 border-dashed border-b w-full h-14 pl-2 pr-4"
                    value={props.item.title}
                    onChange={e => {
                        props.editTitleItem(e, props.listPosition);
                    }}
                />
            )}
            {props.item.editing === false ? (
                <button
                    type="button"
                    className="rounded-full absolute cursor-default white px-1 z-30 text-white right-0 mr-12"
                    onClick={() => {
                        props.toggleEditItem(props.listPosition);
                    }}
                >
                    <Edit
                        className="edit cursor-pointer inline-block relative -mt-1 hover:text-blue-700"
                        size={20}
                    />
                </button>
            ) : (
                <button
                    type="button"
                    className="rounded-full text-xs absolute cursor-default px-1 z-3 right-0 mr-10 bg-green-500 text-white px-2 py-1"
                    onClick={() => {
                        props.toggleEditItem(props.listPosition);
                    }}
                >
                    Opslaan
                </button>
            )}
            <button
                type="button"
                className="rounded-full absolute cursor-default white px-1 z-40 text-white right-0 mr-2"
                onClick={() => {
                    const confirmDelete = window.confirm(
                        `Wil je '${props.item.title}' verwijderen?`,
                    );

                    if (confirmDelete) {
                        props.removeItem(props.listPosition);
                    }
                }}
            >
                <Trash2
                    className="bin cursor-pointer inline-block relative -mt-1 hover:text-red-700"
                    size={20}
                />
            </button>
        </li>
    );
};

export default SortableTodoItem;
