import React, { Component } from 'react';
import PropTypes from 'prop-types';

class ContainerMain extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className="h-full min-h-screen wa-main-bg -mt-6 wa-main-bg relative mx-auto flex-row md:flex container z-10">
        {this.props.children}
      </div>
    );
  }
}

ContainerMain.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
};

export default ContainerMain;
