import React, { Component } from 'react';
import PropTypes from 'prop-types';

class TextHeadingThree extends Component {
  constructor(props) {
    super(props);
  }
  null;
  render() {
    return (
      <h3
        className={`font-bold text-left text-gray-700 text-lg mt-5 ${
          this.props.centerText ? 'text-center' : ''
        }`}
      >
        {this.props.children}
      </h3>
    );
  }
}

TextHeadingThree.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
};

export default TextHeadingThree;
