import React from 'react';
import ContentLoader from 'react-content-loader';

const LoaderActielijst = () => (
  <ContentLoader height={85} className="w-full">
    <rect x="0" y="0" rx="3" ry="3" width="100%" height="22" />

    <rect x="0" y="30" rx="3" ry="3" width="100%" height="22" />

    <rect x="0" y="60" rx="3" ry="3" width="100%" height="22" />
  </ContentLoader>
);

export default LoaderActielijst;
