import React, { Component } from 'react';
import { X } from 'react-feather';

class DashboardUitlegModuleAnalyse extends React.Component {
  render() {
    return (
      <React.Fragment>
        <div>
          <div className="w-screen h-screen z-40 left-0 top-0 bg-black opacity-25 fixed" />
          <div className="flex z-50 justify-center items-center w-screen h-screen top-0 left-0 fixed">
            <div className="max-w-xl bg-white rounded shadow-lg p-8 relative info-uitleg-popup overflow-y-scroll transition-short">
              <X
                className="absolute right-0 top-0 mr-2 mt-2 cursor-pointer"
                onClick={this.props.toggleAnalysePopUp}
              />
              <h3 className="font-bold text-xl text-gray-700 mb-4">
                Uitleg Dashboard
              </h3>

              <p className="my-2">
                Hier zie je een samenvatting van wat jij zelf hebt
                ingevuld in de app WorkAppic in de 7 stappen. Als er
                op het dashboard bij een onderdeel staat ‘nog
                verwerken’, dan heb je dat onderdeel nog niet volledig
                verwerkt. Doe dit eerst, zodat je het maximale uit
                WorkAppic haalt. Heb je dit gedaan? Lees dan hieronder
                welke informatie je uit het dashboard kunt halen.
              </p>

              <p>
                <b>Hoe leuk vindt je jouw huidige werk nog?</b> Kijk
                naar:
              </p>
              <ul className="list-disc pl-5 my-2">
                <li>
                  <u>cijfer huidig werkleven:</u> en vergelijk deze
                  met het cijfer van gewenst werkleven
                </li>
                <li>
                  <u>huidig werkleven:</u> hoeveel minder leuke punten
                  en leuke punten je hebt en hoeveel minder leuke
                  punten je nog leuker kunt maken
                </li>
                <li>
                  <u>gewenst werkleven:</u> in hoeverre jouw huidige
                  werkleven voldoet aan jouw eisen en wensen van jouw
                  gewenste werkleven
                </li>
              </ul>
              <p>
                Is jouw conclusie dat je (voorlopig) verder wilt met
                je huidige werkleven? Voer dan alle acties uit die in
                jouw actielijst staan voor een leuker werkleven.
              </p>

              <p className="my-2">
                <b>
                  Als je niet verder wilt met jouw huidige werkleven,
                  welke mogelijkheden heb je dan?
                </b>{' '}
                Kijk naar:
              </p>
              <ul className="list-disc pl-5 my-2">
                <li>
                  <u>cijfer huidig werkleven:</u> dit is het cijfer
                  dat je wilt bereiken voor jouw gewenste werkleven
                </li>
                <li>
                  <u>gewenst werkleven:</u> welke eisen en wensen heb
                  je hier opgeschreven.
                </li>
                <li>
                  <u>mogelijkheden:</u> welke mogelijkheden staan hier
                  om te komen naar jouw gewenste werkleven
                </li>
              </ul>

              <div>
                Is jouw conclusie dat je iets anders wilt doen in jouw
                werkleven? Bekijk dan de mogelijkheden die je hebt en
                ga hiermee aan de slag. Hulp nodig? Bekijk alle andere{' '}
                <a
                  href="http://workappic.com/oplossingen/"
                  rel="noreferrer"
                  target="_blank"
                >
                  oplossingen
                </a>{' '}
                van WorkAppic.
              </div>
              <br />

              <p className="my-2">
                <b>
                  EN DAARNA KAN JE WORKAPPIC ALTIJD EN OVERAL BLIJVEN
                  GEBRUIKEN OM TE BLIJVEN WERKEN AAN JOUW WERKLEVEN!
                  TIJDENS WORKAPPIC WORKSHOPS, IN JE PAUZES, OP DE
                  BANK, HOU DE APP VAN WORKAPPIC UPTODATE:).
                </b>
              </p>
              <span
                onClick={this.props.toggleAnalysePopUp}
                className="py-2 my-2 px-5 mt-4 text-center bg-green-600 hover:bg-green-800 font-bold text-white md:text-lg rounded-lg cursor-pointer shadow-md inline-block"
              >
                Sluit Popup
              </span>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
export default DashboardUitlegModuleAnalyse;
