import React from 'react';
import Lightbulb from "../Lightbulb";

const Tooltip = (props) => {
    return (
        <Tooltip
            content={
                <div className="max-w-sm">
                    <p>
                        {props.children}
                    </p>
                </div>
            }
            className="inline-block z-20"
        >
        </Tooltip>
    );
};

export default Tooltip;
