import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

function MogelijkheidItem(props) {
  return (
    <li className="py-2 border-t text-gray-800 border-gray-200 block text-left leading-loose	md:leading-normal md:text-center">
      <Link to="/mijn-werkleven/5/1">{props.children}</Link>
    </li>
  );
}

function MogelijkheidItemCijferLabel(props) {
  return (
    <span
      className={`px-4 py-1 bg-${props.color}-200 text-${props.color}-700 rounded-full inline ml-6`}
    >
      {props.cijfer}
    </span>
  );
}

class DashboardMogelijkheidListItem extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    const item = this.props.item;
    const index = this.props.index;

    if (item.cijfer !== undefined) {
      let parsedCijfer = 0;
      if (typeof item.cijfer === 'string') {
        parsedCijfer = parseFloat(
          item.cijfer.replace(',', '.').replace(' ', ''),
        );
      } else if (typeof item.cijfer === 'number') {
        parsedCijfer = item.cijfer;
      } else {
        parsedCijfer = 0;
        return;
      }
      let color = '';
      if (parsedCijfer >= 6) {
        color = 'green';
      }
      if (parsedCijfer < 6 && parsedCijfer > 4) {
        color = 'orange';
      }
      if (parsedCijfer <= 4) {
        color = 'red';
      }
      if (isNaN(parsedCijfer)) {
        color = 'green';
      }

      return (
        <MogelijkheidItem key={index}>
          {index + 1}. {item.title}
          <MogelijkheidItemCijferLabel
            cijfer={parsedCijfer.toFixed(1)}
            color={color}
          />
        </MogelijkheidItem>
      );
    } else {
      return (
        <MogelijkheidItem key={index}>
          {index + 1}. {item.title}
          <MogelijkheidItemCijferLabel
            cijfer={'?'}
            color={'orange'}
          />
        </MogelijkheidItem>
      );
    }
  }
}

DashboardMogelijkheidListItem.propTypes = {};

DashboardMogelijkheidListItem.defaultProps = {};

export default DashboardMogelijkheidListItem;
