import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import { X } from 'react-feather';

function SimplePopUp(props) {
  return (
    <div>
      <div className="w-screen h-screen z-40 left-0 top-0 bg-black opacity-25 fixed" />
      <div className="flex z-50 justify-center items-center w-screen h-screen top-0 left-0 fixed px-6">
        <div className="max-w-xl bg-white rounded shadow-lg p-8 py-10 relative info-uitleg-popup transition-short overflow-y-scroll">
          <X
            className="absolute right-0 top-0 mr-2 mt-2 cursor-pointer"
            onClick={props.closePopUp}
          />
          {props.children}
        </div>
      </div>
    </div>
  );
}

class DashboardWatGaIkNuDoen extends Component {
  constructor(props) {
    super(props);
    this.state = {
      HWOpen: false,
      MogelijkhedenOpen: false,
      TwijfelOpen: false,
      ContactGlenn: false,
    };
    this.closePopUp = this.closePopUp.bind(this);
  }

  closePopUp() {
    this.setState({
      HWOpen: false,
      MogelijkhedenOpen: false,
      TwijfelOpen: false,
      ContactGlenn: false,
    });
  }

  render() {
    return (
      <React.Fragment>
        {this.state.ContactGlenn ? (
          <SimplePopUp closePopUp={this.closePopUp}>
            <p>
              Mail, app of bel Glenn met jouw specifieke vraag:
              hi@workappic.com of +316-11121684. Een kwartier helpt
              Glenn jou gratis. Is er meer tijd nodig na een kwartier?
              Dan laat Glenn dit direct aan jou weten.
            </p>
          </SimplePopUp>
        ) : null}
        {this.state.HWOpen ? (
          <SimplePopUp closePopUp={this.closePopUp}>
            <p>
              Wil je nu direct acties uitvoeren van de actielijst?
            </p>
            <Link
              to="/to-do-lijst"
              className="py-2 my-2 px-5 mt-4 text-center bg-green-600 hover:bg-green-800 font-bold text-white md:text-lg rounded-lg cursor-pointer shadow-md inline-block w-full md:w-auto"
            >
              actielijst
            </Link>
            <span
              className="py-2 my-2 px-5 mt-4 text-center bg-green-600 hover:bg-green-800 font-bold text-white md:text-lg rounded-lg cursor-pointer shadow-md inline-block w-full md:w-auto ml-0 md:ml-4"
              onClick={() => {
                alert(
                  'We werken hier momenteel hard aan, binnenkort is dit beschikbaar! 😄',
                );
              }}
            >
              Mijn huidige werkleven leuker maken
            </span>
          </SimplePopUp>
        ) : null}
        {this.state.TwijfelOpen ? (
          <SimplePopUp closePopUp={this.closePopUp}>
            <p className="mb-4">
              Dan heb je waarschijnlijk nog wat meer informatie nodig.
              <br />
              Lees hier hoe je jouw dashboard kan analyseren:
            </p>
            <ul>
              <li className="py-1">
                - kijk naar cijfer huidig en gewenst{' '}
              </li>
              <li className="py-1">
                - kijk naar aantal minder leuke en leuke punten van je
                huidige werkleven
              </li>
              <li className="py-1">
                - kijk naar hoeveel belangrijke punten je hebt in jouw
                gewenste werkleven en aan hoeveel punten jouw huidige
                werkleven voldoet of nog kan voldoen{' '}
              </li>
              <li className="py-1">
                - kijk naar hoeveel punten je wilt in jouw gewenste
                werkleven en aan hoeveel punten jouw huidige werkleven
                voldoet{' '}
              </li>
              <li className="py-1">
                - kijk naar welk cijfer je geeft aan jouw
                mogelijkheden{' '}
              </li>
            </ul>

            <p className="my-2">
              Kan je nu wel een keuze maken? Zo ja, top! Zo nee, kies
              dan alsnog en start een module om deze mogelijkheid te
              onderzoeken.
            </p>
            <span
              onClick={this.closePopUp}
              className="py-2 my-2 px-5 mt-4 text-center bg-green-600 hover:bg-green-800 font-bold text-white md:text-lg rounded-lg cursor-pointer shadow-md inline-block"
            >
              Sluit Popup
            </span>
          </SimplePopUp>
        ) : null}
        <div>
          <Link
            to="/to-do-lijst"
            className="block md:inline-block w-full py-2 mx-0 md:mx-3 my-2 px-5 mt-6 text-center bg-green-600 hover:bg-green-800 font-bold text-white md:text-lg rounded-lg cursor-pointer shadow-md"
          >
            Direct acties uitvoeren
          </Link>
          <span
            className="block md:inline-block w-full py-2 mx-0 md:mx-3 my-2 px-5 mt-6 text-center bg-green-600 hover:bg-green-800 font-bold text-white md:text-lg rounded-lg cursor-pointer shadow-md"
            onClick={() => {
              alert(
                'We werken momenteel hard aan de ontwikkeling van de modulen. Deze komen eraan! 😄',
              );
            }}
          >
            Een module starten
          </span>
          <span
            className="block md:inline-block w-full py-2 mx-0 md:mx-3 my-2 px-5 mt-6 text-center bg-green-600 hover:bg-green-800 font-bold text-white md:text-lg rounded-lg cursor-pointer shadow-md"
            onClick={() => {
              this.setState({
                ContactGlenn: !this.state.ContactGlenn,
              });
            }}
          >
            Ik twijfel dus ik ga Glenn contacten voor advies
          </span>
        </div>
      </React.Fragment>
    );
  }
}
export default DashboardWatGaIkNuDoen;
