import React, { useState } from 'react';
import { goalStyling, dayAndMonth } from '../../functions/utils';
import PopUpGoalSettings from '../../components/Template/PopUpGoalSettings';
import { updateCurrentGoal } from '../../functions/goalsHelper';
import { withAuthentication } from '../../components/Session';
import { withFirebase } from '../../components/Firebase';
import { CheckCircle, Circle, Settings } from 'react-feather';

const horizontalLine = {
  '&:after': {
    content: '',
    display: 'block',
    backgroundColor: 'gray',
    marginLeft: '-2px;',
  },
};

const TimelineNode = props => {
  const [goalActionList, setGoalActionList] = useState(false);

  const toggleGoalSettings = () => {
    if (!props.isDashboard) {
      setGoalActionList(prev => !prev);
    }
  };

  const toggleGoalState = () => {
    if (!props.isDashboard) {
      let state = '';
      if (props.goal.state === 'failed') {
        state = props.goal.state === 'failed' ? 'passed' : 'failed';
      } else {
        state = props.goal.state === 'active' ? 'done' : 'active';
      }
      updateCurrentGoal(
        props.firebase,
        props.authUser,
        props.goal,
        props.goal.labelNaam,
        props.goal.deadline,
        props.goal.actions,
        state,
        props.goal.notes,
      ).then(() => {
        props.setLoading(true);
      });
    }
  };

  const countActiveActions = actions => {
    const activeActions = actions.filter(e => {
      return !e.voltooid;
    });
    return activeActions.length;
  };

  return (
    <div
      key={props.goal.labelNaam + '_' + props.index}
      className={
        `w-4/4 flex flex-row mt-8 ${
          props.goal.type === 'werk'
            ? 'justify-end'
            : 'justify-start -ml-3'
        } ` + goalStyling(props.goal.state)
      }
      style={{
        ...horizontalLine,
        marginRight: '-0.8rem',
        marginLeft: '-0.8rem',
      }}
    >
      {goalActionList && (
        <PopUpGoalSettings
          goal={props.goal}
          goals={props.goals}
          toggleGoalSettingPopUp={toggleGoalSettings}
          setLoading={props.setLoading}
        />
      )}
      {props.goal.type === 'prive' && window.innerWidth >= 500 && (
        <div className={`md:w-20 flex flex-col justify-center`}>
          <div
            className="text-xs"
            style={{ marginLeft: '-2px' }}
          >
            {dayAndMonth(props.goal.deadline)}
          </div>
        </div>
      )}
      <div
        className={`w-11/12 md:w-3/4 flex flex-row items-center justify-center sm:mt-0 mt-3 py-2 md:px-5 md:px-6 text-white md:text-lg rounded-full
        ${
          props.goal.type === 'werk'
            ? 'bg-green-1000 hover:bg-green-800'
            : 'bg-green-1100 hover:bg-gray-500'
        }`}
      >
        {props.goal.state === 'done' ||
        props.goal.state === 'passed' ? (
          <span
            className={
              'md:w-1/12 cursor-pointer actie-item-voltooid '
            }
            onClick={e => {
              toggleGoalState(e);
            }}
          >
            <CheckCircle
              size={window.innerWidth >= 500 ? 30 : 15}
              style={{
                backgroundColor: 'white',
                borderRadius: '50%',
              }}
              className="mx-2 md:mr-2 wa-green-color -mt-1 relative select-none pointer-events-none actie-icon-voltooid border-2 border-white"
            />
          </span>
        ) : (
          <span
            className={'w-1/12 cursor-pointer actie-item-onvoltooid '}
            onClick={e => {
              toggleGoalState(e);
            }}
          >
            <Circle
              size={window.innerWidth >= 500 ? 30 : 15}
              style={{
                borderRadius: '50%',
              }}
              className="mx-2 md:mr-2 text-white -mt-1 relative select-none pointer-events-none actie-icon-onvoltooid"
            />
            <CheckCircle
              size={window.innerWidth >= 500 ? 30 : 15}
              style={{
                backgroundColor: 'white',
                borderRadius: '50%',
              }}
              className="mx-2 md:mr-2 wa-green-color -mt-1 relative select-none pointer-events-none actie-icon-voltooid border-2 border-white"
            />
          </span>
        )}
        <span
          className={'mx-4 w-7/12 md:w-10/12 cursor-pointer md:ml-2'}
          onClick={() => {
            toggleGoalSettings(props.goal);
          }}
        >
          <div
            className={'flex flex-col items-start md:px-5'}
            style={{}}
          >
            <div className="container text-xs md:text-lg text-left truncate ...">
              {props.goal.labelNaam}
            </div>
            <div
              className="flex text-xs items-center"
              style={{ marginTop: '-2px' }}
            >
              <div
                className="text-xs md:text-sm"
                style={{ marginTop: '-1px' }}
              >
                ({countActiveActions(props.goal.actions)}) acties
              </div>
            </div>
          </div>
        </span>
        {!props.isDashboard && (
          <span
            className={'w-1/12'}
            onClick={() => {
              toggleGoalSettings(props.goal);
            }}
          >
            <Settings
              size={window.innerWidth >= 500 ? 25 : 15}
              className="-ml-4 text-xs md:mr-2 relative cursor-pointer -mt-1 relative"
            />
          </span>
        )}
      </div>
      {props.goal.type === 'werk' && window.innerWidth >= 500 && (
        <div className="md:w-20 flex flex-col justify-center">
          <div className="text-xs">
            {dayAndMonth(props.goal.deadline)}
          </div>
        </div>
      )}
    </div>
  );
};

export default withAuthentication(withFirebase(TimelineNode));
