import React from 'react';
import { isBefore, subDays } from 'date-fns';
import { makeDate } from '../../functions/utils';

const Deadline = ({ date, state, onClick }) => {
  const formatedDate = makeDate(date);
  return (
      <>
          {' '}
          {date ? (
              <div
          className={
            isBefore(date, subDays(new Date(), 1)) && state !== true
              ? 'flex flex-col w-48 px-4 mr-4 text-center justify-center ml-auto bg-red-200 text-xs md:text-sm text-red-700 rounded-full'
              : 'flex flex-col w-48 px-4 mr-4 text-center justify-center ml-auto bg-green-200 text-xs md:text-sm text-green-800 rounded-full'
          }
          onClick={onClick}
        >
                  {formatedDate}
              </div>
      ) : null}
      </>
  );
};

export default Deadline;
