import React, { Component } from 'react';
import SelectLabel from './../SelectLabel';
import { withFirebase } from '../Firebase';
import { toast } from 'react-toastify';
import Tooltip from 'react-tooltip-lite';
import Lightbulb from './../Lightbulb';

import { Plus, X } from 'react-feather';

import PopUpContainer from './../PopUpContainer';

import {
  addThought,
  createNewAction,
  updateActionObj,
} from '../../functions/actionsHelper';
import { addThoughtToGoal } from '../../functions/goalsHelper';
import { withAuthentication } from '../Session';
import { withRouter } from 'react-router-dom';

class PopUpVoegActieToe extends Component {
  constructor(props) {
    super(props);

    this.state = {
      nieuweActie: {
        actieNaam: '',
        hoeActie: '',
        hoeNodig: '',
        hoeAndere: '',
        labels: [],
      },
      saving: false,
      saved: false,
      selectedLabel: [],
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleLabelChange = this.handleLabelChange.bind(this);
    this.saveToDatabase = this.saveToDatabase.bind(this);
    this.makeLabelSlug = this.makeLabelSlug.bind(this);
    this.saveNewLabelToDatabase = this.saveNewLabelToDatabase.bind(
      this,
    );
  }

  makeLabelSlug(item) {
    /* eslint-disable */
    return item
      .toString()
      .trim()
      .toLowerCase()
      .replace(/\s+/g, '-')
      .replace(/[^\w\-]+/g, '')
      .replace(/\-\-+/g, '-')
      .replace(/^-+/, '')
      .replace(/-+$/, '');
  }

  handleLabelChange(newValue, initialize) {
    console.log('actie', this.props.title);
    console.log('authUser', this.props.authUser);

    this.setState({ selectedLabel: newValue });
  }

  // Functie om een nieuw aangemaakt label op te slaan naar Firebase
  saveNewLabelToDatabase(nieuwLabel) {
    const firebase = this.props.firebase;
    const docRef = firebase.db
      .collection('users')
      .doc(this.props.userID)
      .collection('actielijst')
      .doc('labels');

    const that = this;

    docRef
      .update({
        currentLabels: firebase.fieldValue.arrayUnion(nieuwLabel),
      })
      .then(function() {
        console.log('Doel successfully written to db!');
        if (that.props.addLabelFromTwijfelFlow) {
          that.props.addLabelFromTwijfelFlow(nieuwLabel);
        }
      })
      .catch(function(error) {
        console.error('Error writing document: ', error);
      });
  }

  handleChange(event) {
    console.log('event', event.target.value)
    const name = event.target.name;
    const value = event.target.value;
    this.setState(prevState => ({
      nieuweActie: {
        ...prevState.nieuweActie,
        [name]: value,
      },
    }));
  }

  saveToDatabase(exitStatus) {
    const that = this;
    const firebase = that.props.firebase;

    let nieuweActie = {
      actieNaam: this.state.nieuweActie.actieNaam,
      hoeForm: {
        hoeActie: this.state.nieuweActie.hoeActie,
        hoeNodig: this.state.nieuweActie.hoeNodig,
        hoeAndere: this.state.nieuweActie.hoeAndere,
        openState: true,
        savedState: true,
      },
      labels: this.state.nieuweActie.labels,
      voltooid: false,
    };

    const newAction = createNewAction(nieuweActie.actieNaam);
    const updateNewCreatedAction = updateActionObj(
      newAction,
      this.state.selectedLabel.UUID,
      this.state.nieuweActie.actieNaam,
      null,
      null,
      nieuweActie.hoeForm,
      false,
    );

    if (this.state.selectedLabel.UUID) {
      addThoughtToGoal(
        firebase,
        this.props.authUser,
        this.state.selectedLabel,
        updateNewCreatedAction,
      ).then(result => {
        this.setState({
          acties: [],
          nieuweActie:  {
            actieNaam: '',
            hoeForm: {
              hoeActie: '',
              hoeNodig: '',
              hoeAndere: '',
              openState: true,
              savedState: false,
            },
            labels: [],
            voltooid: false,
          }
        });
        toast('Actie verplaats naar het Doel! 👍');
      });
    } else {
      /* function saveDocument() {
         docRef
           .update({
             acties: this.props.firebase.fieldValue.arrayUnion(nieuweActie),
           })
           .then(function () {
             toast('Op actielijst gezet! 👍');

             let element = document.getElementById('add-action-module');
             if (element) element.scrollTop = 0;

             that.setState({
               saved: true,
               saving: false,
             });
             setTimeout(function () {
               that.setState({
                 saving: false,
                 saved: false,
               });
               if (exitStatus === true) {
                 closeModule();
               } else {
                 that.setState({
                   nieuweActie: {
                     actieNaam: '',
                     hoeActie: '',
                     hoeNodig: '',
                     hoeAndere: '',
                     labels: [],
                   },
                 });
               }
             }, 1000);
           })
           .catch(function (error) {
             console.error('Error writing document: ', error);
           });
       }
       docRef
         .get()
         .then(function (doc) {
           if (doc.exists) {
             saveDocument();
           } else {
             docRef.set({
               acties: [],
             });
             saveDocument();
           }
         })
         .catch(function (error) {
           console.log('Error getting document:', error);
         });
     }*/
      addThought(
        firebase,
        this.props.authUser,
        newAction,
      ).then( () => {
        this.setState({
          acties: [],
          nieuweActie:  {
            actieNaam: '',
            hoeForm: {
              hoeActie: '',
              hoeNodig: '',
              hoeAndere: '',
              openState: true,
              savedState: false,
            },
            labels: [],
            voltooid: false,
          }
        });
        toast('Gedachte toegevoegd aan inbox! 👍');
      });
    }
  }

  render() {
    let standardLabel = this.state.nieuweActie.labels;
    return (
        <PopUpContainer closeModule={this.props.closeModule}>
            <h3 className="font-bold text-xl text-gray-700">
                Voeg een actie toe{' '}
                {this.props.title ? (
                    <React.Fragment>
                        voor
                        <span className="italic ml-1">
                            "{this.props.title}"
                        </span>
                    </React.Fragment>
          ) : null}
            </h3>
            <form onSubmit={e => e.preventDefault()}>
                <h3 className="font-bold text-gray-700 mb-1 mt-4">
                    Wat kan ik doen?
                </h3>
                <input
            required
            name="actieNaam"
            className="border w-full px-2 py-1"
            type="text"
            autoComplete="off"
            value={this.state.nieuweActie.actieNaam}
            onChange={this.handleChange}
          />
                <h3 className="font-bold text-gray-700 mb-1 mt-4">
                    Hoe ga ik dat doen?
                </h3>
                <input
            name="hoeActie"
            className="border w-full px-2 py-1"
            type="text"
            autoComplete="off"
            value={this.state.nieuweActie.hoeActie}
            onChange={this.handleChange}
          />
                <h3 className="font-bold text-gray-700 mb-1 mt-4">
                    Wat heb je hiervoor nodig?
                    <Tooltip
              content={
                  <div className="max-w-sm">
                      <p>Bijvoorbeeld:</p>
                      <ul>
                          <li>- middelen (tijd en geld) </li>
                          <li>- bepaalde vaardigheden</li>
                          <li>- bepaalde kennis </li>
                          <li>- bepaalde informatie </li>
                      </ul>
                  </div>
              }
              className="inline-block z-20"
            >
                        <Lightbulb />
                    </Tooltip>
                </h3>
                <input
            name="hoeNodig"
            className="border w-full px-2 py-1"
            type="text"
            autoComplete="off"
            value={this.state.nieuweActie.hoeNodig}
            onChange={this.handleChange}
          />
                <h3 className="font-bold text-gray-700 mb-1 mt-4">
                    Koppel de actie aan een doel
                    <Tooltip
              content={
                  <div className="max-w-sm">
                      <ul>
                          <li>
                              - Heb je bijvoorbeeld een antwoord van iemand
                              nodig?
                          </li>
                          <li>- Kan je hulp bij iemand vragen?</li>
                          <li>
                              - Heeft iemand bepaalde kennis, vaardigheden of
                              informatie waar je vragen over kan stellen?
                          </li>
                      </ul>
                  </div>
              }
              className="inline-block z-20"
            >
                        <Lightbulb />
                    </Tooltip>
                </h3>
                <input
            name="hoeAndere"
            className="border w-full px-2 py-1"
            type="text"
            autoComplete="off"
            value={this.state.nieuweActie.hoeAndere}
            onChange={this.handleChange}
          />
                <h3 className="font-bold text-gray-700 mb-1 mt-4">
                    Kies een doel of maak die later aan!
                    <Tooltip
              content={
                  <div className="max-w-sm">
                      <p>Bijvoorbeeld:</p>
                      <ul>
                          <li>- huidige baan leuker maken</li>
                          <li>- takenpakket veranderen</li>
                          <li>- persoonlijke ontwikkeling</li>
                          <li>- samenwerking verbeteren</li>
                      </ul>
                  </div>
              }
              className="inline-block z-20"
            >
                        <Lightbulb />
                    </Tooltip>
                </h3>
                <SelectLabel
                    handleLabelChange={this.handleLabelChange}
                    standardLabel={standardLabel}
                  />
                <div className="flex flex-col flex-wrap">
                    <button
                      type="submit"
                      onClick={
                        this.state.nieuweActie?.actieNaam?.length > 0
                          ? () => this.saveToDatabase(false)
                          : null
                      }
                      className="py-2 px-5 mt-3 text-center bg-green-600 hover:bg-green-800 font-bold text-white md:text-lg rounded-lg cursor-pointer shadow-md"
                    >
                        <Plus className="inline mr-2 relative -mt-1" />
                        Voeg toe aan Actielijst
                    </button>
                </div>
            </form>
        </PopUpContainer>
    );
  }
}

export default withAuthentication(withFirebase(PopUpVoegActieToe));
