import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Checkbox from "../Checkbox";

class FormFieldListItemLeukerBeterMaken extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className="flex mb-4">
        <label className="bg-white block mt-2 shadow rounded px-4 py-4 mr-2 cursor-pointer hover:shadow-lg transition-short">
          <span>
            <Checkbox
              handleChecked={() => {
                if (!this.props.checked) {
                  this.props.openPuntLeukerMaken();
                }
              }}
              onClick={e => {
                if (!this.props.checked) {
                  this.props.setLeukerMakenBool(true, this.props.itemIndex, this.props.arrayProp);
                }
              }}
              key={1}
              index={1}
              initialCheckboxValuesArray={this.props.arrayProp}
              checkBoxValue={this.props.leukerMakenBool !== undefined ? this.props.checked : false }
            />
          </span>
          <span>Ja, kan beter</span>
        </label>
        <label className="bg-white block mt-2 shadow rounded px-4 py-4 mr-2 cursor-pointer hover:shadow-lg transition-short">
          <Checkbox
              handleChecked={e => {
                if (!this.props.checked) {
                  this.props.setLeukerMakenBool(false, this.props.itemIndex, this.props.arrayProp);
                }
              }}
              key={1}
              index={1}
              initialCheckboxValuesArray={this.props.arrayProp}
              checkBoxValue={this.props.leukerMakenBool !== undefined ? !this.props.checked : false }
          />
          <span>Nee</span>
        </label>
      </div>
    );
  }
}

FormFieldListItemLeukerBeterMaken.propTypes = {
  leukerMakenBool: PropTypes.bool,
  checked: PropTypes.bool,
  openPuntLeukerMaken: PropTypes.func,
};

export default FormFieldListItemLeukerBeterMaken;
