// ---------------------------
// Filter Functies voor acties
// ---------------------------

export const filterBySelectedGoal = (selectedGoal, array) => {
  // In deze functie filteren we alle acties op basis of
  // ze het actieve label in het label array hebben
  if (array && array.length > 0) {
    if (selectedGoal === undefined) {
      return [];
    }
    const newArray = array.filter((element, index) => {
      if (element.labels !== undefined) {
        const bool = element.labels.some(label => {
          return label.UUID === selectedGoal.UUID;
        });
        return bool;
      }
    });
    return newArray;
  }
};
