import React, { Component } from 'react';
import PropTypes from 'prop-types';

class DashboardContainerMultipleCards extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className="flex-col md:flex">
        {this.props.children}
      </div>
    );
  }
}

DashboardContainerMultipleCards.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
};

export default DashboardContainerMultipleCards;
