import React from 'react';
import { CheckCircle, Circle } from 'react-feather';

const CheckedCircle = props => {
  return (
    <>
      {props.voltooid ? (
        <span
          className="cursor-pointer"
          data-index={1}
          onClick={e => {
            props.toggleVoltooid(e);
          }}
        >
          <CheckCircle className="inline-block mr-2 relative wa-green-color -mt-1 relative select-none pointer-events-none border-2 border-white"  />
        </span>
      ) : (
        <span
          className="cursor-pointer actie-item-onvoltooid"
          data-index={2}
          onClick={e => {
            props.toggleVoltooid(e);
          }}
        >
          <CheckCircle className="mr-2 relative wa-green-color -mt-1 relative select-none pointer-events-none actie-icon-voltooid border-2 border-white" />
          <Circle className="mr-2 relative text-gray-500 -mt-1 relative select-none pointer-events-none actie-icon-onvoltooid border-2 border-white" />
        </span>
      )}
    </>
  );
};

export default CheckedCircle;
