import React from 'react';
import { X } from 'react-feather';

class InfoUitlegModule extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      open: props.firstVisit,
    };
    this.toggleOpenState = this.toggleOpenState.bind(this);
  }

  toggleOpenState() {
    this.setState({
      open: !this.state.open,
    });
  }

  render() {
    return (
      <React.Fragment>
        <div
          onClick={this.toggleOpenState}
          className="ml-5 wa-bg-bordeaux text-white text-xl cursor-pointer rounded-full	overflow-hidden w-48 border-white border-2 z-50	"
        >
          <div className="flex justify-center align-middle">
            <h2 className="font-hairline py-2 rounded">
              Uitleg
            </h2>
          </div>
        </div>
        {this.state.open ? (
          <div>
            <div className="w-screen h-screen z-40 left-0 top-0 bg-black opacity-25 fixed" />
            <div className="flex z-50 justify-center items-center w-screen h-screen top-0 left-0 fixed">
              <div className="max-w-xl bg-white rounded shadow-lg p-8 relative info-uitleg-popup overflow-y-scroll">
                <X
                  className="absolute right-0 top-0 mr-2 mt-2 cursor-pointer"
                  onClick={this.toggleOpenState}
                />
                {/* <h3 className="font-bold text-xl text-gray-700 mb-4">
                  Gebruiksaanwijzing
                </h3> */}
                {this.props.children}
              </div>
            </div>
          </div>
        ) : null}
      </React.Fragment>
    );
  }
}

export default InfoUitlegModule;
