import React, { Component } from 'react';
import PropTypes from 'prop-types';

class FormFieldListItemTitle extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className="inline-block">
        <span
          className="text-lg cursor-pointer md:pl-6 pl-0 mt-10 md:mt-0 ml-3 inline-block text-gray-700 select-none"
          onClick={this.props.toggleOpen}
        >
          {this.props.index + 1}. {this.props.title}
        </span>
      </div>
    );
  }
}

FormFieldListItemTitle.propTypes = {
  toggleOpen: PropTypes.func,
  title: PropTypes.string,
};

export default FormFieldListItemTitle;
