import React, { useState, useEffect } from 'react';
import {
  ChevronDown,
  ChevronUp,
  Plus,
  Copy,
  Clock,
  X,
  CheckCircle,
  Circle,
  List,
  Tag,
  Edit,
  Calendar,
  FileText,
  Trash2,
  HelpCircle,
} from 'react-feather';

const PopUpEditTitle = props => {
  const [title, setTitle] = useState();

  useEffect(() => {
    setTitle(props.title);
  }, [props.title]);

  return (
    <React.Fragment>
      <div className="w-screen h-screen z-40 left-0 top-0 bg-black opacity-25 fixed"></div>
      <div className="w-full h-screen pb-6 pt-20 fixed z-50 left-0 top-0 flex justify-center items-center mx-auto text-base">
        <div className="bg-white overflow-y-auto max-h-full rounded shadow-lg p-12 relative">
          <X
            className="absolute right-0 top-0 mr-5 mt-5 cursor-pointer"
            onClick={props.closePopup}
          />
          <form
            className="text-gray-700"
            onSubmit={e => {
              e.preventDefault();
              props.wijzigCurrentLabelNaam(e);
              props.closePopup();
            }}
          >
            <label className="block font-bold">
              Wijzig het doel naam
            </label>
            <input
              className="inline-block border border-gray-300 rounded-l mt-2 px-2 py-1"
              type="text"
              name="naam"
              value={title}
              onChange={e => {
                setTitle(e.target.value);
              }}
            />
            <button
              type="submit"
              className="inline-block mt-1 cursor-pointer bg-green-600 hover:bg-green-800 text-white rounded-r px-5 py-1 font-bold"
            >
              Wijzig
            </button>
          </form>
        </div>
      </div>
    </React.Fragment>
  );
};

export default PopUpEditTitle;
