import React, { useState, Component } from 'react';
import PropTypes from 'prop-types';
import { toast } from 'react-toastify';
import { NavLink } from 'react-router-dom';
import {
  Tag,
  Menu,
  Plus,
  X,
  ArrowDownCircle,
  Calendar,
} from 'react-feather';
import Lightbulb from './../Lightbulb';
import Tooltip from 'react-tooltip-lite';

import PopUpVoegDoelToe from '../../components/PopUpVoegDoelToe/PopUpVoegDoelToe';
import InfoUitlegModule from '../InfoUitlegModule';
import ButtonMain from '../ButtonMain';
import SidebarGoals from '../Template/Sidebar-Goals';
import ContainerMain from '../ContainerMain';
import { getAllGoals } from '../../functions/goalsHelper';
import { withAuthentication } from '../Session';
import { withFirebase } from '../Firebase';
import { pages } from "../../text.json";
import ActionListNav from "../Atoms/Actionlist-Nav";

function VoegLabelToe(props) {
  return (
      <React.Fragment>
          <div className="w-screen h-screen z-40 left-0 top-0 bg-black opacity-25 fixed"></div>
          <div className="w-full h-screen pb-6 pt-20 fixed z-50 left-0 top-0 flex justify-center items-center mx-auto">
              <div className="bg-white overflow-y-auto max-h-full rounded shadow-lg p-12 relative">
                  <X
            className="absolute right-0 top-0 mr-5 mt-5 cursor-pointer"
            onClick={props.closePopup}
          />
                  <form
            className="text-gray-700"
            onSubmit={e => {
              e.preventDefault();
              if (e.target['naam'].value.length === 0) {
                toast('Vul een doel in');
                return;
              }
              props.submitNewLabel(e);
              props.closePopup();
            }}
          >
                      <label className="block font-bold">
                          Voeg een doel toe
                      </label>
                      <input
              className="inline-block border border-gray-300 rounded- mt-2 px-2 py-1"
              type="text"
              name="naam"
              minLength="1"
            />
                      <button
              type="submit"
              className="w-32 inline-block mt-1 cursor-pointer bg-green-600 hover:bg-green-800 text-white rounded-r px-5 py-1 font-bold"
            >
                          Voeg toe
                      </button>
                  </form>
              </div>
          </div>
      </React.Fragment>
  );
}

