import React from 'react';

const RadioButton = ({ label, required, name, value, checked }) => {
  return (
    <div className="inline-block">
      <label className="bg-white block mt-2 shadow pr-4 mr-2 cursor-pointer hover:shadow-lg transition-short">
        {label}
        {required ? (
          <input
            type="radio"
            name={name}
            value={value}
            className="ml-2 inline-block"
            defaultChecked={checked}
            required
          />
        ) : (
          <input
            type="radio"
            name={name}
            value={value}
            className="ml-2 inline-block"
            defaultChecked={checked}
          />
        )}
      </label>
    </div>
  );
};

export default RadioButton;
