import React, { useState, useEffect } from 'react';
import { AlertTriangle, Calendar, HelpCircle } from 'react-feather';
import Tooltip from 'react-tooltip-lite';
import Lightbulb from '../Lightbulb';
import ButtonSmallGreen from "../Atoms/Button-Small-Green";
import TitleGreen from "../Atoms/Titles-Green";

const DeadLinePicker = props => {
  const [selectedDeadline, setSelectedDeadline] = useState();
  const [buttonName, setButtonName] = useState(
    'Voeg een streefdatum toe',
  );

  const selectedDeadlineDate = e => {
    setSelectedDeadline(e.target.value);
    props.setActionDeadline(e);
    setButtonName('Wijzig de streefdatum');
  };

  const updateWithSelection = e => {
    props.edit(selectedDeadline);
    setButtonName('Streefdatum');
  };

  useEffect(() => {
    if (props.actionDeadline) {
      setSelectedDeadline(props.actionDeadline);
      setButtonName('Streefdatum');
    }
  }, []);

  return (

      <div className="flex flex-col md:flex-row md:items-center mt-4">
        <TitleGreen title="Streefdatum" />
        <span className="mr-8 ml-6">
          <Tooltip
              content={
                <div className="max-w-sm p-4 pl-6">
                  <div>
                    Kies de datum waarop jij een actie/doel behaalt wilt
                    hebben. Een doel met de datum wordt automatisch op
                    jouw tijdlijn gezet. Op de tijdlijn kan je dit
                    altijd nog aanpassen 😄.
                  </div>
                  <br />
                  <div>
                    Handigheidje: WorkAppic herinnert jou aan het
                    uitvoeren van de acties en doelen door het sturen
                    van e-mailnotificaties.
                  </div>
                </div>
              }
              className="inline-block z-20"
          >
            <Lightbulb />
          </Tooltip>
        </span>

        <input
          type="date"
          onChange={e => {
            selectedDeadlineDate(e);
          }}
          value={selectedDeadline}
          defaultValue={''}
          data-index={1}
          name="Streefdatum"
          required
          min="2019-01-01"
          className="text-xs datum-input-actielijst-item black border-r-0 pl-2 bg-gray-100 border text-base md:text-lg mr-10 rounded-l"
        />
        <ButtonSmallGreen onClick={updateWithSelection} title="Voeg streefdatum toe" />
      </div>

  );
};
export default DeadLinePicker;
