import React from 'react';

const TitleInput = ({ name, value, handleOnChange, placeholder }) => (
  <>
    <input
      name={name}
      placeholder={placeholder}
      value={value}
      onChange={e => {
        handleOnChange(e);
      }}
      className="w-full rounded px-1 text-gray-800 text-base md:text-2xl mt-4 mb-4 border border-gray-300 mr-4"
    />
  </>
);

export default TitleInput;
