import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import format from 'date-fns/format';
import nlLocale from 'date-fns/locale/nl';
import differenceInWeeks from 'date-fns/difference_in_weeks';
import differenceInMonths from 'date-fns/difference_in_months';
import differenceInYears from 'date-fns/difference_in_years';

import { BarChart, X, ExternalLink } from 'react-feather';
import {
  AreaChart,
  Area,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from 'recharts';

const CustomTooltip = ({ active, payload, label }) => {
  if (active && payload) {
    let emoji = '';
    switch (payload[0].payload.smiley) {
      case '5':
        emoji = '😄';
        break;
      case '4':
        emoji = '🙂';
        break;
      case '3':
        emoji = '😐';
        break;
      case '2':
        emoji = '😔';
        break;
      case '1':
        emoji = '😭';
        break;
      default:
        emoji = '😐';
    }

    return (
      <div className="px-4 text-3xl py-2 text-gray-700 font-bold rounded shadow bg-white">
        <p>
          {`${payload[0].value} - `}
          <span
            role="img"
            aria-label="emoji"
            className="no-underline"
          >
            {emoji}
          </span>
        </p>
      </div>
    );
  }

  return null;
};

class WerklevenCijferChart extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeFilter: 'alles',
    };
    this.switchFilter = this.switchFilter.bind(this);
  }

  switchFilter(filterName) {
    this.setState({
      activeFilter: filterName,
    });
  }

  render() {
    let data = this.props.arrayItems.map(object => ({ ...object }));

    function weekFilter(data) {
      return data.filter(item => {
        const diff = differenceInWeeks(
          new Date(),
          item.datum.toDate(),
        );
        if (diff <= 1) {
          return item;
        }
      });
    }

    function maandFilter(data) {
      return data.filter(item => {
        const diff = differenceInMonths(
          new Date(),
          item.datum.toDate(),
        );
        if (diff <= 1) {
          return item;
        }
      });
    }

    function jaarFilter(data) {
      return data.filter(item => {
        const diff = differenceInYears(
          new Date(),
          item.datum.toDate(),
        );
        if (diff <= 1) {
          return item;
        }
      });
    }

    if (this.state.activeFilter === 'week') {
      data = weekFilter(data);
    } else if (this.state.activeFilter === 'maand') {
      data = maandFilter(data);
    } else if (this.state.activeFilter === 'jaar') {
      data = jaarFilter(data);
    }

    data.forEach(item => {
      if (typeof item.datum.toDate === 'function') {
        item.datum = format(item.datum.toDate(), 'DD/MM/YYYY', {
          locale: nlLocale,
        });
      } else {
        item.datum = format(item.datum, 'DD/MM/YYYY', {
          locale: nlLocale,
        });
      }
      item.cijfer = parseInt(item.cijfer);
    });

    return (
      <React.Fragment>
        <div className="py-4 md:absolute sm:relative xs:relative top-0 right-0 flex justify-center md:justify-end">
          <span
            onClick={() => this.switchFilter('week')}
            className={`${
              this.state.activeFilter === 'week'
                ? 'text-white border-green-600 bg-green-600'
                : 'text-green-600 hover:border-green-600 hover:bg-green-600 hover:text-white border-green-600'
            } font-semibold py-2 text-center border-2 rounded px-2 md:px-4 text-xs md:text-sm cursor-pointer mr-2 md:mr-4`}
          >
            Week
          </span>
          <span
            onClick={() => this.switchFilter('maand')}
            className={`${
              this.state.activeFilter === 'maand'
                ? 'text-white border-green-600 bg-green-600'
                : 'text-green-600 hover:border-green-600 hover:bg-green-600 hover:text-white border-green-600'
            } font-semibold py-2 text-center border-2 rounded px-2 md:px-4 text-xs md:text-sm cursor-pointer mr-2 md:mr-4`}
          >
            Maand
          </span>
          <span
            onClick={() => this.switchFilter('jaar')}
            className={`${
              this.state.activeFilter === 'jaar'
                ? 'text-white border-green-600 bg-green-600'
                : 'text-green-600 hover:border-green-600 hover:bg-green-600 hover:text-white border-green-600'
            } font-semibold py-2 text-center border-2 rounded px-2 md:px-4 text-xs md:text-sm cursor-pointer mr-2 md:mr-4`}
          >
            Jaar
          </span>
          <span
            onClick={() => this.switchFilter('alles')}
            className={`${
              this.state.activeFilter === 'alles'
                ? 'text-white border-green-600 bg-green-600'
                : 'text-green-600 hover:border-green-600 hover:bg-green-600 hover:text-white border-green-600'
            } font-semibold py-2 text-center border-2 rounded px-2 md:px-4 text-xs md:text-sm cursor-pointer mr-2 md:mr-4`}
          >
            Alles
          </span>
        </div>
        <ResponsiveContainer width="100%" height="85%">
          <AreaChart data={data}>
            <defs>
              <linearGradient
                id="colorUv"
                x1="0"
                y1="0"
                x2="0"
                y2="1"
              >
                <stop
                  offset="5%"
                  stopColor="#4FD1C5"
                  stopOpacity={0.8}
                />
                <stop
                  offset="95%"
                  stopColor="#4FD1C5"
                  stopOpacity={0}
                />
              </linearGradient>
            </defs>
            <YAxis
              type="number"
              domain={[0, 'dataMax + 1']}
              interval={0}
              ticks={[0, 2, 4, 6, 8, 10]}
            />
            <XAxis
              dataKey="datum"
              interval={0}
              angle={-45}
              textAnchor="end"
              height={75}
              padding={{ right: 10 }}
            />
            <Area
              isAnimationActive={false}
              dot={{ stroke: '#319795', strokeWidth: 10 }}
              type="monotone"
              dataKey="cijfer"
              stroke="#319795"
              strokeWidth={4}
              fillOpacity={1}
              fill="url(#colorUv)"
            />
            <Tooltip content={<CustomTooltip />} />
          </AreaChart>
        </ResponsiveContainer>
      </React.Fragment>
    );
  }
}

class DashboardCardCijferChart extends React.Component {
  render() {
    return (
      <div className="w-full mb-6 md:mb-0 md:w-3/4 md:mr-6 md:ml-6">
        <div
          id="DashboardCardCijferChart"
          className="bg-white relative h-full overflow-hidden p-5"
        >
          <Link to="/mijn-werkleven/2/3">
            <h2 className="font-bold pl-8 mb-4 text-gray-800 text-2xl">
              2. Cijfers{' '}
              <ExternalLink className="inline-block ml-4 relative text-gray-500 icon-position" />
            </h2>
          </Link>
          {this.props.arrayItems ? (
            <WerklevenCijferChart
              arrayItems={this.props.arrayItems.huidigCijfers}
            />
          ) : (
            <span className="italic font-sm text-gray-700 w-full text-center mt-10 block">
              Hier komen je Huidig Werkleven cijfers te staan
            </span>
          )}
        </div>
      </div>
    );
  }
}

DashboardCardCijferChart.propTypes = {
  arrayItems: PropTypes.object,
};

export default DashboardCardCijferChart;
