// De volgende pagina's hebben een boolean waarde voor de First Visit van de pagina:
// - Dashboard
// - ToDoLijst (Actielijst)
// - Mijn Werkleven 1
// - Mijn Werkleven 2.1 (/mijn-werkleven/2/1)
// - Mijn Werkleven 2.3 (/mijn-werkleven/2/3)
// - Mijn Werkleven 3
// - Mijn Werkleven 4
// - Mijn Werkleven 5
// Deze state is terug te vinden in de 'users' collection in Firebug onder de property 'firstVisit'

// Functie om een specifieke FirstVisit boolean voor een gebruiker te wijzigen naar 'false'
function saveNewFirstVisitStateToDB(uid, firebase, page, dataObject) {
  let newObject = dataObject;
  newObject.firstVisit[page] = false;
  let docRef = firebase.db.collection('users').doc(uid);
  docRef
    .set(newObject)
    .then(function() {
      console.log('Document successfully written!');
    })
    .catch(function(error) {
      console.error('Error writing document: ', error);
    });
}

// Functie om het FirstVisit object in de database te initializen
function initializeFirstVisitObject(uid, firebase, page, dataObject) {
  let docRef = firebase.db.collection('users').doc(uid);
  let newObject = dataObject;
  newObject.firstVisit = {
    dashboard: true,
    toDoLijst: true,
    timeline: true,
    MW1: true,
    MW2_1: true,
    MW2_2: true,
    MW3: true,
    MW4: true,
    MW5: true,
  };

  docRef
    .set(newObject)
    .then(function() {
      console.log('Document successfully written!');
    })
    .catch(function(error) {
      console.error('Error writing document: ', error);
    });
}

// Functie om de FirstVisit waarde van een specifieke pagina te checken
function checkFirstVisitPage(
  uid,
  firebase,
  page,
  setFirstVisitStateUitleg,
) {
  const docRef = firebase.db.collection('users').doc(uid);
  let firstVisitBoolean = null;

  // API Call
  docRef
    .get()
    .then(function(doc) {
      if (doc.exists) {
        // Als de gebruiker nog geen firstVisit object heeft initializen we deze
        if (doc.data().firstVisit === undefined) {
          initializeFirstVisitObject(uid, firebase, page, doc.data());
          // Save firstVisit state naar het component van waaruit deze functie wordt aangeroepen
          // Op basis van deze state wordt de uitleg popup bij het eerste bezoek getoond
          // Aangezien in deze if(){} de gebruiker nog geen object heeft is de firstVisit waarde altijd true
          setFirstVisitStateUitleg(true);
        } else {
          // Als de gebruiker een doc.data().firstVisit object heeft halen we de waarde van de pagina hieruit en zetten we deze in de state met setFirstVisitStateUitleg()
          firstVisitBoolean = doc.data().firstVisit[page];
          setFirstVisitStateUitleg(firstVisitBoolean);

          // Indien de firstVisitBoolean true is zetten we deze voor de specifieke pagina op false met de functie saveNewFirstVisitStateToDB()
          if (firstVisitBoolean === true) {
            let dataObject = doc.data();
            saveNewFirstVisitStateToDB(
              uid,
              firebase,
              page,
              dataObject,
            );
          }
        }
      }
    })
    .catch(function(error) {
      console.log('Error getting document:', error);
    });
}

export default checkFirstVisitPage;
