import React from 'react';
import { CheckCircle, Circle } from 'react-feather';

const ActionToolTitle = ({
  action,
  title,
  state,
  toggleState,
  index,
}) => {
  return (
      <>
          {state ? (
              <span
          className="cursor-pointer"
          data-index={index}
          onClick={e => {
            toggleState(e, action);
          }}
        >
                  <CheckCircle className="inline-block mr-2 relative text-green-500 -mt-1 relative select-none pointer-events-none" />
              </span>
      ) : (
          <span
          className="cursor-pointer actie-item-onvoltooid"
          data-index={index}
          onClick={e => {
            toggleState(e, action);
          }}
        >
              <CheckCircle className="mr-2 relative text-green-500 -mt-1 relative select-none pointer-events-none actie-icon-voltooid" />
              <Circle className="mr-2 relative text-gray-500 -mt-1 relative select-none pointer-events-none actie-icon-onvoltooid" />
          </span>
      )}
          <span className="flex  w-full	flex-grow	cursor-pointer">{title}</span>
      </>
  );
};

export default ActionToolTitle;
