import React, { Component } from 'react';
import { PieChart, Pie, Cell, ResponsiveContainer } from 'recharts';

const COLORS = ['#38a169', '#f56565', '#ECC94B'];

class DashboardGewenstWerklevenPieChart extends Component {
  render() {
    const data = [
      {
        name: 'Huidige Werkleven Voldoet',
        value: this.props.hwVoldoet,
      },
      {
        name: 'Huidige Werkleven Voldoet Niet',
        value: this.props.hwVoldoetNiet,
      },
      {
        name: 'waarvan huidige werkleven nog kan voldoen',
        value: this.props.hwVoldoetNietTeVerbeteren,
      },
    ];

    return (
      <ResponsiveContainer height={225} width="100%">
        <PieChart>
          <Pie
            data={data}
            // labelLine={false}
            isAnimationActive={false}
            label
            outerRadius={80}
            fill="#8884d8"
            dataKey="value"
          >
            {data.map((entry, index) => (
              <Cell
                key={`cell-${index}`}
                fill={COLORS[index % COLORS.length]}
              />
            ))}
          </Pie>
        </PieChart>
      </ResponsiveContainer>
    );
  }
}

export default DashboardGewenstWerklevenPieChart;
