import React from 'react';
import {NavLink} from "react-router-dom";

const ActionListNav = (props) => {
    return (
        <li className="text-white mb-2 font-xl">
            <NavLink
                activeClassName="is-active cursor-default text-white underline"
                className="transition-fast hover:translate-t-2px hover:text-gray-100 text-white"
                to={props.to}
                exact
                onClick={() => {
                    if (props.navOpen) {
                        props.toggleNav();
                    }
                }}
            >
                {props.title}
            </NavLink>
            <span className="count ml-2 text-sm text-white no-underline	">({props.amount})</span>
        </li>
    );
};

export default ActionListNav;
