import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { withFirebase } from '../Firebase';
import { withAuthentication } from '../Session';
import { X } from 'react-feather';
import { toast } from 'react-toastify';
import Lightbulb from './../Lightbulb';
import Tooltip from 'react-tooltip-lite';

class UploadProfilePicture extends React.Component {
  constructor(props) {
    super(props);
    this.submitData = this.submitData.bind(this);
    this.state = {
      uploadedPF: false,
      uploading: false,
    };
  }

  submitData(e) {
    const file = document.getElementById('fileupload').files[0];

    if (file === undefined) {
      toast.error('selecteer eerst een afbeelding');
      return;
    }

    if (file.size > 1000000) {
      toast.error('Zorg ervoor dat je afbeelding kleiner is dan 1MB');
      return;
    }
    const storage = this.props.storage;
    const imagesRef = storage
      .ref()
      .child(`profile_pictures/${this.props.authUser.uid}`);

    const that = this;

    imagesRef
      .put(document.getElementById('fileupload').files[0])
      .then(function(snapshot) {
        console.log('Uploaded a blob or file!');
        that.setState({
          uploadedPF: true,
        });
        toast('Succesvol geupload! 👍');
      });

    this.props.userUploadsProfilePicture(this.props.authUser.uid);
  }

  render() {
    return (
      <form
        className="flex border border-4 border-dashed border-gray-300 my-10"
        id="uploadProfilePicture"
        encType="multipart/form-data"
      >
        <input
          id="fileupload"
          name="myfile"
          type="file"
          accept="image/*"
          className="px-4 py-4 rounded-lg w-full"
        />
        <input
          type="submit"
          value={this.state.uploadedPF ? 'Geupload! :)' : 'Uploaden'}
          id="submit"
          className="py-2 px-5 bg-blue-600 hover:bg-blue-800 font-bold text-white md:text-lg rounded-lg shadow-md cursor-pointer"
          onClick={e => {
            e.preventDefault();
            this.submitData(e);
          }}
        />
      </form>
    );
  }
}

class PopupStepper extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeStep: 0,
    };
    this.volgendeStep = this.volgendeStep.bind(this);
    this.vorigeStep = this.vorigeStep.bind(this);
  }

  volgendeStep(e) {
    if (this.state.activeStep === 5) {
      this.props.toggleDashboardPopUp();
    }
    const nextStep = this.state.activeStep + 1;
    this.setState({
      activeStep: nextStep,
    });
  }
  vorigeStep() {
    console.log(this.state.activeStep);
    if (this.state.activeStep === 0) {
      return;
    }
    const vorigeStep = this.state.activeStep - 1;
    this.setState({
      activeStep: vorigeStep,
    });
  }

  render() {
    return (
      <React.Fragment>
        <div>
          <div className="w-screen h-screen z-40 left-0 top-0 bg-black opacity-25 fixed" />
          <div className="flex z-50 justify-center items-center w-screen h-screen top-0 left-0 fixed">
            <div
              className="max-w-xl bg-white rounded shadow-lg p-8 relative info-uitleg-popup overflow-y-scroll transition-short"
              id="stepper"
            >
              <X
                className="absolute right-0 top-0 mr-2 mt-2 cursor-pointer"
                onClick={this.props.toggleDashboardPopUp}
              />

              <h3 className="font-bold text-xl text-gray-700 mb-4">
                Uitleg WorkAppic - {this.state.activeStep + 1}/5
              </h3>

              {this.state.activeStep === 0 ? (
                <React.Fragment>
                  <p>
                    Welkom
                    {this.props.username
                      ? ` ${this.props.username.trim()}`
                      : ''}
                    !
                  </p>
                  <p className="mb-4 mt-2">
                    <a
                      className="underline text-blue-600 inline-block"
                      href="https://drive.google.com/open?id=0BySrvvVDqrATcWFmUU9Ga0tTaXRWeEVtaXRtR1pOaVlkQzE4"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      Bekijk hier hoe je WorkAppic als app op je
                      smartphone zet.
                    </a>
                  </p>
                  <p className="mb-4 mt-2">
                    Maak WorkAppic persoonlijk en upload je
                    profielfoto:
                  </p>
                  <UploadProfilePicture
                    storage={this.props.firebase.storage}
                    authUser={this.props.authUser}
                    userUploadsProfilePicture={
                      this.props.userUploadsProfilePicture
                    }
                  />
                </React.Fragment>
              ) : null}
              {this.state.activeStep === 1 ? (
                <React.Fragment>
                  <p className="mb-2">
                    In WorkAppic ga je aan de slag met alles wat je
                    denkt over en vindt van jouw werkleven: ideeën,
                    acties, vragen, twijfels.
                  </p>
                  <p className="mb-4">
                    Bepaal zelf hoe je WorkAppic invult:
                    <ul className="list-disc pl-4">
                      <li>beknopt of uitgebreid.</li>
                      <li>in één keer of in meerdere keren. </li>
                    </ul>
                    Elk onderdeel kan je later nog aanvullen wanneer
                    het jou uitkomt.
                  </p>
                </React.Fragment>
              ) : null}

              {this.state.activeStep === 2 ? (
                <React.Fragment>
                  <p className="mb-2">
                    WorkAppic gaat over jouw werkleven en bestaat uit
                    zeven onderdelen:
                  </p>
                  <div className="uitleg-menu"></div>
                  <p className="mb-4">
                    Na het invullen van deze onderdelen, kom je terug
                    op jouw Dashboard. Dat is een samenvatting van wat
                    je hebt ingevuld in de zeven onderdelen.
                  </p>
                </React.Fragment>
              ) : null}
              {this.state.activeStep === 3 ? (
                <React.Fragment>
                  <p className="mb-4">
                    Klik op opslaan als je tussentijds gegevens wilt
                    opslaan. Als je op volgende of voltooien klikt,
                    dan slaat het systeem ook je gegevens op.
                  </p>
                </React.Fragment>
              ) : null}
              {this.state.activeStep === 4 ? (
                <React.Fragment>
                  <div className="mb-4">
                    Loop je even vast of heb je meer inspiratie nodig?
                    Klik dan op
                    <Tooltip
                      content={
                        <div className="max-w-sm">
                          <p>Hier vind je dan meer uitleg of tips.</p>
                        </div>
                      }
                      className="inline-block z-20"
                    >
                      <Lightbulb />
                    </Tooltip>
                    zodat je weer verder kunt.
                    <br />
                    Veel plezier!
                  </div>
                </React.Fragment>
              ) : null}
              <div className="mt-6">
                <div
                  className={`py-2 px-5 font-bold text-white md:text-lg rounded-lg shadow-md inline-block mr-4 ${
                    this.state.activeStep !== 0
                      ? `bg-green-600 hover:bg-green-800 cursor-pointer`
                      : `bg-green-600 opacity-50 cursor-not-allowed`
                  } `}
                  onClick={this.vorigeStep}
                >
                  <span className="select-none">Vorige</span>
                </div>
                <Link
                  to={
                    this.state.activeStep !== 4
                      ? `#`
                      : `/mijn-werkleven/1/1`
                  }
                  className="py-2 px-5 bg-green-600 hover:bg-green-800 font-bold text-white inline-block md:text-lg rounded-lg shadow-md cursor-pointer"
                  onClick={this.volgendeStep}
                >
                  <span className="select-none">
                    {this.state.activeStep !== 4
                      ? `Volgende`
                      : `Start!`}
                  </span>
                </Link>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
export default withAuthentication(withFirebase(PopupStepper));
