import React from 'react';
import { toast } from 'react-toastify';
import { Loader } from 'react-feather';
import { Link } from 'react-router-dom';

const Stepper = ({
  canSave,
  isLoading,
  previous,
  next,
  nextTitle = 'Volgende',
}) => {
  return (
    <div className="bg-white md:p-3 flex w-full flex-row justify-end ">
      {canSave && (
        <button
          onClick={() => {
            //saveCurrentState();
            toast('Opgeslagen! 👍');
          }}
          className="block bg-green-1200 text-white w-full text-sm md:w-auto mb-2 mr-1 md:mb-0 md:inline-block py-2 px-5 border-2 font-bold hover:text-white hover:bg-green-600 md:text-lg rounded-full shadow-md md:mr-4 cursor-pointer"
        >
          {isLoading ? (
            <Loader
              className="mr-2 inline-block"
              id="loader_loading"
              size={30}
            />
          ) : null}
          Opslaan
        </button>
      )}
      <Link
        to={previous}
        className="block w-full md:w-auto mb-2 mr-1 md:mb-0 md:inline-block py-2 px-5 bg-green-1200 hover:bg-green-800 font-bold text-white md:text-lg rounded-full shadow-md md:mr-4 text-center"
      >
        Vorige
      </Link>

      <Link
        to={next}
        className="block w-full md:w-auto mb-2 md:mb-0 md:inline-block py-2 px-5 bg-red-1000 hover:bg-green-800 font-bold text-white md:text-lg rounded-full shadow-md text-center"
      >
        {nextTitle}
      </Link>
    </div>
  );
};

export default Stepper;
