import React, { useState } from 'react';

import ActionTools from '../Molecules/ActionsTools';
import Deadline from '../Atoms/Deadline';
import ActionToolTitle from '../Atoms/ActionTool-Title';
import {
  deleteActionFromFB,
  updateAction,
} from '../../functions/actionsHelper';
import { withAuthentication } from '../Session';
import { withFirebase } from '../Firebase';
import { makeDate } from '../../functions/utils';
import { CheckCircle, X } from 'react-feather';

const ActionToolsBar = props => {
  // get action deadline or get from goal
  const [datePickerOpen, setDatePickerOpen] = useState(false);
  const actionDeadline = props.action.deadline;

  const deleteClickedAction = () => {
    deleteActionFromFB(
      props.firebase,
      props.authUser,
      props.action,
      props.goal,
    ).then(result => {
      //Reload the page when closing the popup
      //this is a hack untill redux is implemented
      console.log('result delete', result);
      props.deleteAction(result);
      props.setLoading(true);
    });
  };

  const newActionDate = e => {
    let action = props.action;
    action.deadline = e.target.value;

    updateAction(
      props.firebase,
      props.authUser,
      action,
      props.goal,
    ).then(() => {
      setDatePickerOpen(prev => !prev);
    });
  };

  const toggleDoneState = (e, action) => {
    e.preventDefault();
    if (!props.showSettings) {
      //action.toggleVoltooid = !action.toggleVoltooid
      //console.log('toggle done state action',action.toggleVoltooid)
      props.updateCurrentAction(action);
    }
  };

  return (
      <>
          {props.completedActions.length >= 1 && props.index === 0 && (
          <div className="flex">
              <h1 className="font-bold text-xl md:text-2xl text-gray-700 mb-4">
                  <CheckCircle className="inline-block mr-2 relative text-green-300 icon-position" />
                  Voltooide acties
              </h1>
          </div>
      )}
          {props.action.voltooid === props.isDone && (
          <div
          key={props.index}
          className={`flex text-gray-700 text-lg border-b my-2 pl-4 ${
            props.action.voltooid === true ? 'line-through' : null
          }`}
        >
              <ActionToolTitle
            action={props.action}
            title={props.action.actieNaam}
            state={props.action.voltooid}
            toggleState={toggleDoneState}
            index={props.index}
          />
              {!datePickerOpen && (
              <Deadline
              date={actionDeadline}
              state={props.action.voltooid}
              onClick={() => {
                setDatePickerOpen(prev => !prev);
              }}
            />
          )}
              {datePickerOpen && (
              <div className="flex flex-row px-2 text-center  items-center justify-center ml-auto">
                  <div>
                      <input
                  type="date"
                  onChange={e => {
                    newActionDate(e);
                  }}
                  defaultValue={actionDeadline}
                  data-index={2}
                  name="deadline"
                  min="2019-01-01"
                  className="datum-input-actielijst-item border-r-0 pl-2 bg-gray-100 border text-lg rounded-l text-black mr-1"
                />
                  </div>
                  <div
                className="cursor-pointer rounded-full	border-2 border-black  ml-2"
                onClick={() => {
                  setDatePickerOpen(prev => !prev);
                }}
              >
                      <X className="text-black cursor-pointer" size={16} />
                  </div>
              </div>
          )}
              <ActionTools
            action={props.action}
            deleteAction={deleteClickedAction}
            index={props.index}
            goal={props.goal}
            goals={props.goals}
            firebase={props.firebase}
            user={props.authUser}
            setLoading={props.setLoading}
            toggleDoubtFlow={props.toggleDoubtFlow}
            doubtFlow={props.doubtFlow}
            updatedAction={props.updatedAction}
            setSelectedGoal={props.setSelectedGoal}
            selectedAction={props.selectedAction}
          />
          </div>
      )}
      </>
  );
};

export default withAuthentication(withFirebase(ActionToolsBar));
