import React from 'react';
import RadioButton from '../Atoms/RadioButton';

const ToggleGoalType = props => (
  <div
    onChange={e => {
      props.onToggle(e.target.value);
    }}
  >
    <RadioButton
      label={props.labelOne}
      required={false}
      name={props.name}
      value={props.valueOne}
      checked={props.selectedType === 'werk' ? true : false}
    />
    <RadioButton
      label={props.labelTwo}
      required={false}
      name={props.name}
      value={props.valueTwo}
      checked={props.selectedType === 'prive' ? true : false}
    />
  </div>
);

export default ToggleGoalType;
