import React, { useState, useEffect } from 'react';
import ActielijstNotitie from '../ActielijstNotitie';
import { makeDateToday } from '../../functions/utils';
import AddButton from "../Atoms/AddButton";
import ButtonSmallGreen from "../Atoms/Button-Small-Green";
import TitleGreen from "../Atoms/Titles-Green";

const AddNotes = props => {
  const [notes, setNotes] = useState([]);
  const [note, setNote] = useState('');

  const handleChange = e => {
    let value = e.target.value;
    setNote(value);
  };

  const addNotes = () => {
    let constructNote = {
      content: note,
      date: new Date(),
    };
    let tmpNotes = [...notes, constructNote];
    props.addNotes(tmpNotes);
  };

  useEffect(() => {
    setNotes(props.notes);
  }, [props.notes]);

  return (
    <div className="flex flex-col w-full items-start md:mt-4">
      <TitleGreen title="Nieuwe notitie" />
      <textarea
        placeholder="Type hier je notitie..."
        name="nieuweNotitie"
        value={props.note}
        data-index={1}
        onChange={handleChange}
        className="text-base border rounded w-full md:p-4 mt-2 mb-2"
      />
      <ButtonSmallGreen title="Notitie toevoegen" onclick={addNotes} />

      <div className="flex flex-row w-full items-start md:mt-6">
        {notes
          ? notes
              .slice(0)
              .reverse()
              .map((note, index) => {
                return (
                  <ActielijstNotitie
                    key={index}
                    notitieInhoud={
                      note.inhoud ? note.inhoud : note.content
                    }
                    notitieDatum={
                      note.date.seconds
                        ? new Date(note.date.seconds * 1000)
                        : note.date
                    }
                  />
                );
              })
          : null}
      </div>
    </div>
  );
};

export default AddNotes;
