  loadLabelsDB(user) {
    // GET Request voor de labels
    // Deze worden vervolgens gemuteerd om in de navigatie weer gegeven te worden
    let firebase = this.props.firebase;
    let that = this;

    var docRefLabel = firebase.db
      .collection('users')
      .doc(user.uid)
      .collection('actielijst')
      .doc('labels');

    docRefLabel
      .get()
      .then(function(doc) {
        if (doc.exists) {
          let initialState = [];
          if (
            doc.data().currentLabels &&
            doc.data().currentLabels.length > 0
          ) {
            doc.data().currentLabels.forEach(item => {
              const itemValue = item.value || item.labelNaam;
              initialState.push({
                labelNaam: itemValue,
                labelSlug: that.makeLabelSlug(itemValue),
                UUID: item.UUID,
              });
            });
          }
          that.setState({ labels: initialState });
        } else {
          /*docRef.set({
            labels: [],
          });*/
        }
      })
      .catch(function(error) {
        console.log('Error getting document:', error);
      });
  }

  loadAllActions() {
    let firebase = this.props.firebase;
    let that = this;

    firebase.auth.onAuthStateChanged(function(user) {
      let allGoals = [];
      let newActions = [];
      let thoughts = [];

      if (user) {
        checkFirstVisitPage(
          user.uid,
          firebase,
          'toDoLijst',
          that.setFirstVisitStateUitleg,
        );

        //HIER WORDEN DE ACTIES OPGEHAALD VAN DE OUDE LABELS
        //HAAL DE NIEUWE OP EN VOEG DIE ERBIJ]
        getAllActions(firebase, user).then(result => {
          newActions = result;
          that.loadLabelsDB(user);
          //console.log('all newActions', newActions);
          //console.log('all thoughts', thoughts);
          let allActions = thoughts.concat(newActions);
          //console.log('all actins', allActions);
          let sortedAction = sortActions(allActions.reverse());
          that.setState({
            acties: sortedAction,
            initialDataLoaded: true,
          });
        });
      }
    });
  }

