  countLabelsAndActions(acties) {
    const that = this;
    let newLabelsArray = that.state.labels;
    // Dit aantal wordt vervolgens toegevoegd in het label als actieAmount key
    if (newLabelsArray) {
      newLabelsArray.map((label, index) => {
        let actieAmount = 0;
        const ogLabelUUID = label.UUID;
        let increaseBool;
        actieAmount = acties.filter(a => {
          return a.voltooid === false;
        });
        label.actieAmount = actieAmount.length;
        return label;
      });
      if (that.state.labels !== newLabelsArray) {
        that.setState({
          labels: newLabelsArray,
        });
      }
    }
  }

  // Gebruikt om de label view te veranderen
  setActiveLabelInState() {
    const currentURL = this.props.location.pathname;
    const currentLabelSlug = currentURL.split('/').pop();

    const activeLabel = this.state.goals.find(
      item => item.labelSlug === currentLabelSlug,
    );
    if (
      activeLabel !== undefined &&
      this.state.activeLabel !== activeLabel
    ) {
      this.setState({
        activeLabel: activeLabel,
      });
    }
    if (activeLabel !== undefined) {
      return activeLabel.labelNaam;
    } else {
      return currentLabelSlug || '';
    }
  }

  // Gebruik
  makeLabelSlug(item) {
    /* eslint-disable */
    return item
      .toString()
      .trim()
      .toLowerCase()
      .replace(/\s+/g, '-')
      .replace(/[^\w\-]+/g, '')
      .replace(/\-\-+/g, '-')
      .replace(/^-+/, '')
      .replace(/-+$/, '');
  }

  submitNewLabel(e) {
    const newLabelName = e.target['naam'].value;
    const newLabel = {
      UUID: uuidv1(),
      label: newLabelName,
      labelNaam: newLabelName,
      labelSlug: this.makeLabelSlug(newLabelName),
      value: newLabelName,
    };
    this.saveNewLabelToDatabase(newLabel);
    toast('Nieuw doel toegevoegd! 👍');
  }

  // Functie om een nieuw aangemaakt label op te slaan naar Firebase
  saveNewLabelToDatabase(nieuwLabel) {
    const firebase = this.props.firebase;
    const docRef = firebase.db
      .collection('users')
      .doc(this.props.authUser.uid)
      .collection('actielijst')
      .doc('labels');

    const that = this;

    docRef
      .update({
        currentLabels: firebase.fieldValue.arrayUnion(nieuwLabel),
      })
      .then(function() {
        let newLabelArrayState = that.state.labels;
        newLabelArrayState.push(nieuwLabel);
        that.setState(
          {
            labels: newLabelArrayState,
          },
          () => console.log('STATE SET!'),
        );
      })
      .catch(function(error) {
        console.error('Error writing document: ', error);
      });
  }

  addLabelFromTwijfelFlow(nieuwLabel) {
    let newLabelArrayState = this.state.labels;
    newLabelArrayState.push(nieuwLabel);
    this.setState(
      {
        labels: newLabelArrayState,
      },
      () => console.log('STATE SET!'),
    );
  }

  updateThought(thought) {
    const that = this;
    let index = this.state.thoughtIndex;
    let selectedGoal = this.state.selectedGoal;
    let selectedThought = this.state.selectedThought;
    let acties = this.state.acties;

    console.log('index', index, selectedThought, selectedGoal.UUID);
    if (index !== undefined) {
      // Item is een actielijst item
      if (selectedThought && selectedGoal.UUID) {
        selectedThought['UUID'] = selectedThought.UUID;

        addThoughtToGoal(
          this.props.firebase,
          this.props.authUser,
          selectedGoal,
          selectedThought,
        ).then(result => {
          acties[index] = result;

          toast('Gedachten verplaatst naar Alle Acties! 👍');
          this.setState({
            acties: acties,
            selectedGoal: [],
          });
        });
      } else {
        //We override the thought in the DB and current actions
        //to force a refresh
        if (thought.deadline === undefined) {
          thought.deadline = null;
        }

        let findIndex = acties.findIndex(
          a => a.aUUID === thought.aUUID,
        );
        acties[findIndex] = thought;

        const firebase = this.props.firebase;

        let docRef = firebase.db
          .collection('users')
          .doc(this.props.authUser.uid)
          .collection('actielijst')
          .doc('acties');

        docRef
          .set({
            acties: acties,
          })
          .then(function() {
            console.log('SAVED');
          })
          .catch(function(error) {
            console.error('Error writing document: ', error);
          });

        //this.setState({acties})
      }
    }
  }

