import React, { useCallback, useEffect, useState } from 'react';
import { X } from 'react-feather';
import EditInputField from '../Molecules/Edit-Input-field';
import AddActionBar from '../Molecules/Add-Action-Bar';
import DeadLinePicker from '../Organisms/DeadLine-Picker';
import ActionToolsBar from '../Organisms/ActionToolsBar';
import Trash from '../Atoms/Trash-Button';
import { ConfirmDelete } from '../../functions/confirmDelete';
import { withAuthentication } from '../Session';
import { withFirebase } from '../Firebase';
import {
  addAction,
  createActionObj,
  updateAction,
} from '../../functions/actionsHelper';
import {
  updateCurrentGoal,
  deleteCurrentGoal,
  getAllGoals,
} from '../../functions/goalsHelper';
import PrimaryActionButton from '../Atoms/PrimaryAction-Button';
import AddNotes from '../Organisms/Add-Notes';
import { toast } from 'react-toastify';
import ToDoLijstTwijfel from '../ToDoLijstTwijfel';
import { day } from '../../functions/utils';
import Tooltip from 'react-tooltip-lite';
import Lightbulb from '../Lightbulb';
import ToDoLijstSidebar from '../ToDoLijstSidebar';
import TitleGreen from "../Atoms/Titles-Green";
import {popups} from "../../text.json";
import ButtonSmallRed from "../Atoms/Button-Small-Red";

const PopUpGoalSettings = props => {
  const [newActionTitle, setNewActionTitle] = useState('');
  const [goalTitle, setGoalTitle] = useState(props.goal.labelNaam);
  const [goalDeadline, setGoalDeadline] = useState(
    props.goal.deadline,
  );
  const [notesCollection, setNotesCollection] = useState(
    props.goal.notes,
  );
  const [completedActions, setCompletedActions] = useState([]);
  const [toggleDoubtFlow, setToggleDoubtFlow] = useState(false);
  const [actions, setActions] = useState([]);
  const [selectedAction, setSelectedAction] = useState();

  const handleInput = e => {
    const value = e.target.value;
    setNewActionTitle(value);
  };

  const setDeadLine = date => {
    setGoalDeadline(date);
  };

  const doubtFlow = () => {
    setToggleDoubtFlow(prev => !prev);
  };

  const deleteGoal = () => {
    deleteCurrentGoal(
      props.firebase,
      props.authUser,
      props.goal,
    ).then(result => {
      if (props.updateAfterDelete) {
        props.updateAfterDelete(result, props.goal);
      }
      props.setLoading(true);
      props.toggleGoalSettingPopUp();
    });
  };

  const updateCurrentAction = action => {
    updateAction(
      props.firebase,
      props.authUser,
      action,
      props.goal,
    ).then(result => {
      let completed = result.filter(a => {
        return a.voltooid == true;
      });
      setCompletedActions(completed);
      let sortedActions = result.sort(function(a, b) {
        return new Date(a.deadline) - new Date(b.deadline);
      });
      setActions(sortedActions);
      props.setLoading(true);
    });
  };

  const deleteAction = actions => {
    setActions(actions);
  };

  const updatedAction = actions => {
    setActions(actions);
  };

  const addNewTitle = newTitle => {
    let foundActions = 0;
    let foundActionsInProp = 0;

    if (newTitle) {
      foundActionsInProp = props.goal.actions.filter(a => {
        return a.actieNaam === newTitle;
      });
      foundActions = actions.filter(a => {
        return a.actieNaam === newTitle;
      });

      if (
        foundActions.length === 0 &&
        foundActionsInProp.length === 0
      ) {
        const newAction = createActionObj(
          newTitle,
          props.goal.UUID,
          props.goal.deadline,
        );

        addAction(
          props.firebase,
          props.authUser,
          props.goal,
          newAction,
        ).then(result => {
          let sortedActions = result.actions.sort(function(a, b) {
            return new Date(a.deadline) - new Date(b.deadline);
          });
          setActions(sortedActions);
          props.setLoading(true);
          setNewActionTitle('');
          toast('Actie toegevoegd aan doel! 👍');
        });
      } else {
        toast('Deze actie bestaat al!');
      }
    }
  };

  const handleNewAction = () => {
    let title = newActionTitle;
    if (title) {
      addNewTitle(title);
    }
  };

  const handleNewActionOnEnter = event => {
    let title = event.target.value;
    if (title) {
      addNewTitle(title);
    } else {
      toast('Deze actie bestaat al!');
    }
  };

  const updateGoalWithValues = () => {
    updateCurrentGoal(
      props.firebase,
      props.authUser,
      props.goal,
      goalTitle,
      goalDeadline,
      actions,
      props.goal.state,
      notesCollection,
    ).then(result => {
      if (props.updateAfterChange) {
        props.updateAfterChange(result, props.goal);
      }
      props.setLoading(true);
      toast('Doel is opgeslagen! 👍');
      //props.toggleGoalSettingPopUp();
    });
  };

  const addNotes = notes => {
    setNotesCollection(notes);
    updateGoalWithValues();
  };

  const setSelectedGoal = action => {
    setSelectedAction(action);
    console.log('selected action', action);
  };

  useEffect(() => {
    if (props.actions) {
      let curActions = props.actions.filter(a => {
        return a.UUID === props.goal.UUID;
      });
      let completedActions = curActions.filter(a => {
        return a.voltooid === true;
      });
      setCompletedActions(
        completedActions.sort(function(a, b) {
          return new Date(a.deadline) - new Date(b.deadline);
        }),
      );
      let sortedActions = curActions.sort(function(a, b) {
        return new Date(a.deadline) - new Date(b.deadline);
      });
      setActions(sortedActions);
    } else {
      let completedActions = props.goal.actions.filter(a => {
        return a.voltooid === true;
      });
      setCompletedActions(
        completedActions.sort(function(a, b) {
          return new Date(a.deadline) - new Date(b.deadline);
        }),
      );
      let sortedActions = props.goal.actions.sort(function(a, b) {
        return new Date(a.deadline) - new Date(b.deadline);
      });
      setActions(sortedActions);
    }
  }, [props.goal]);

  return (
    <>
      <div className="w-screen h-screen z-40 left-0 top-0 bg-black opacity-25 fixed">
        {' '}
      </div>
      <div className="flex z-50 justify-center items-center w-screen h-screen top-0 left-0 fixed">
        <div
          className="relative overflow-y-auto bg-white w-full py-4 px-4 md:w-2/3 m-2 rounded shadow-lg md:p-4"
          style={{ maxHeight: '85%', marginTop: '54px' }}
        >
          <TitleGreen title="Doel" />
          <div className="flex w-full flex-row justify-end absolute right-0 top-0 mr-4 mt-4 cursor-pointer">
            <Trash
              deleteAction={deleteGoal}
              type={'ACTION'}
              name={props.goal.labelNaam}
              UUID={props.goal.UUID}
            />
            <X
              className="cursor-pointer"
              onClick={() => {
                updateGoalWithValues();
                props.toggleGoalSettingPopUp();
              }}
            />
          </div>

          <div className="flex flex-col items-start">
            <EditInputField
              currentTitle={goalTitle}
              handleOnChange={e => {
                setGoalTitle(e.target.value);
              }}
              edit={updateGoalWithValues}
            />
            <DeadLinePicker
              setActionDeadline={e => {
                setDeadLine(e);
              }}
              actionDeadline={goalDeadline}
              edit={setDeadLine}
            />
          </div>

          <div>
            <AddActionBar
              addAction={handleNewAction}
              value={newActionTitle}
              handleOnChange={handleInput}
              placeholder={popups.addaction}
            />
            {actions.length > 0 &&
              actions.map((action, index) => {
                return (
                  <ActionToolsBar
                    goal={props.goal}
                    goals={props.goals}
                    action={action}
                    index={index}
                    updateCurrentAction={updateCurrentAction}
                    toggleGoalSettingPopUp={
                      props.toggleGoalSettingPopUp
                    }
                    setLoading={props.setLoading}
                    showBear={false}
                    showSettings={false}
                    isDone={false}
                    completedActions={[]}
                    doubtFlow={doubtFlow}
                    deleteAction={deleteAction}
                    updatedAction={updatedAction}
                    setSelectedGoal={setSelectedGoal}
                    selectedAction={selectedAction}
                  />
                );
              })}
            {actions.length > 0 &&
              actions.map((action, index) => {
                return (
                  <ActionToolsBar
                    goal={props.goal}
                    goals={props.goals}
                    action={action}
                    index={index}
                    updateCurrentAction={updateCurrentAction}
                    toggleGoalSettingPopUp={
                      props.toggleGoalSettingPopUp
                    }
                    setLoading={props.setLoading}
                    showBear={false}
                    showSettings={false}
                    isDone={true}
                    completedActions={completedActions}
                    toggleDoubtFlow={doubtFlow}
                    deleteAction={deleteAction}
                    setSelectedGoal={setSelectedGoal}
                    selectedAction={selectedAction}
                  />
                );
              })}
          </div>
          <AddNotes notes={notesCollection} addNotes={addNotes} />
          <div className="flex justify-end mt-2">
            <ButtonSmallRed title="Opslaan" onClick={updateGoalWithValues} />
          </div>
        </div>
        {toggleDoubtFlow ? (
          <ToDoLijstTwijfel
            addLabelFromTwijfelFlow={props.addLabelFromTwijfelFlow}
            voegNieuweTwijfelToeVanuitTwijfelFlow={
              props.voegNieuweTwijfelToeVanuitTwijfelFlow
            }
            authUserID={props.authUserID}
            closePopup={doubtFlow}
          />
        ) : null}
      </div>
    </>
  );
};

export default withAuthentication(withFirebase(PopUpGoalSettings));
