import React, { useState, useEffect } from 'react';
import { toast } from 'react-toastify';
import { withRouter } from 'react-router-dom';

import { withFirebase } from '../../components/Firebase';
import { withAuthentication } from '../../components/Session';
import uuidv1 from 'uuid/v1';

import PopUpContainer from '../PopUpContainer';
import AddNotes from '../Organisms/Add-Notes';
import DeadLinePicker from '../Organisms/DeadLine-Picker';
import TitleInput from '../Atoms/Title-Input';
import RadioButton from '../Atoms/RadioButton';

import ToggleGoalType from '../Molecules/Toggle-GoalType';

import PrimaryActionButton from '../Atoms/PrimaryAction-Button';
import {
  addGoalToDB,
  checkAmountOfGoals,
} from '../../functions/goalsHelper';
import { makeLabelSlug } from '../../functions/utils';
import { AlertTriangle, X } from 'react-feather';
import ButtonSmallRed from "../Atoms/Button-Small-Red";
import TitleGreen from "../Atoms/Titles-Green";

const TITLE_TEXT = 'Hier staat het doel';

const PopUpVoegDoelToe = props => {
  const [saving, setSaving] = useState(false);
  const [saved, setSaved] = useState(false);

  const [goal, setGoal] = useState({});
  const [goalTitle, setGoalTitle] = useState(props.title || TITLE_TEXT);
  const [goalType, setGoalType] = useState('werk');
  const [deadline, setDeadline] = useState();
  const [note, setNote] = useState([]);
  const [notesCollection, setNotesCollection] = useState([]);
  const [displayNotes, setDisplayNotes] = useState(
    props.displayNotes || true,
  );
  const [displayWarning, setDisplayWarning] = useState(false);

  const submitNewGoal = e => {
    setDisplayWarning(false);

    e.preventDefault();

    if (goalTitle === TITLE_TEXT) {
      toast('Vul een eigen doelnaam in');
      return;
    } else if (!deadline) {
      setDisplayWarning(true);
      toast('Een doel moet een einddatum hebben!');
      return;
    } else if (
      checkAmountOfGoals(
        props.goals,
        props.goalWork,
        props.goalPrivate,
        props.selectedType || goalType,
        deadline
      )
    ) {
      toast(
        'Je mag maar 3 actieve doelen per maand hebben, houdt focus!',
      );
      return;
    } else {
      addGoalToDB(
        props.firebase,
        props.authUser,
        goalTitle,
        props.selectedType || goalType,
        deadline,
        notesCollection,
      ).then(() => {
        props.setLoading(true);
        props.closeModule();
        toast('Doel is toegevoegd aan de tijdlijn!');
      });
    }
  };

  useEffect(() => {}, [goal]);

  const addNotes = note => {
    setNotesCollection(note);
    /*setNotesCollection(prev => [
      ...prev,
      { content: note, date: Date.now() },
    ]);*/
  };

  const onToggleGoalType = e => {
    console.log('toggled', e.currentTarget.value);
  };

  const newValue = e => {
    const name = e.target.name;
    const value = e.target.value;
    setGoal(inputs => ({ ...inputs, [name]: value }));
  };

  const update = (selectedDeadline) => {
    if (selectedDeadline) {
      setDeadline(selectedDeadline)
    }
    console.log('update', selectedDeadline, goal.deadline);
  };

  return (
    <>
      <div className="w-screen h-screen z-40 left-0 top-0 bg-black opacity-25 fixed"></div>
      <div className="flex z-50 justify-center items-center w-screen h-screen top-0 left-0 fixed">
        <div className="relative bg-white w-full py-4 px-4 md:pl-4 md:w-2/3 max-h-full m-1 rounded shadow-lg p-8 round-sm">
          <TitleGreen title="Doel" />
          <div className="flex w-full flex-col items-end">
            <X
              className="absolute right-0 top-0 mr-4 mt-4 cursor-pointer"
              onClick={e => {
                console.log(
                  'props pop up container',
                  props.closeModule,
                );
                props.closeModule(e);
              }}
            />
          </div>

          <TitleInput
            name={'goalName'}
            handleOnChange={e => {
              setGoalTitle(e.target.value);
            }}
            placeholder={goalTitle}
            value={goalTitle}
          />
          {props.choiceType && (
            <ToggleGoalType
              title={'Type hier je doelnaam...'}
              name={'goal_type'}
              labelOne={'Werk'}
              valueOne={'werk'}
              labelTwo={'Prive'}
              valueTwo={'prive'}
              onToggle={setGoalType}
              selectedType={props.selectedType}
            />
          )}
          <DeadLinePicker
            setActionDeadline={newValue}
            actionDeadline={deadline}
            edit={update}
          />
          {displayWarning && (
            <div className="mt-5">
              <AlertTriangle className="inline-block mr-2 relative text-orange-600 icon-position" />
              <span>Datum moet nog worden toegevoegd</span>
            </div>
          )}
          {props.displayNotes && (
            <AddNotes
              notes={notesCollection}
              value={note}
              addNotes={addNotes}
            />
          )}
          <div className="flex justify-end">
            <ButtonSmallRed title="Zet doel op tijdlijn" onClick={submitNewGoal} />
          </div>
        </div>
      </div>
    </>
  );
};

export default withAuthentication(
  withRouter(withFirebase(PopUpVoegDoelToe)),
);
