import React, { Component } from 'react';

function ActielijstSnelToevoegen(props) {
  return (
    <div className="w-full">
      <div className="bg-white overflow-hidden md:flex">
        <div className="w-full p-4 md:p-5">
          <form
            onSubmit={props.voegNieuweActieToe}
            className="flex-row md:flex block"
          >
            <input
              name="nieuweActie"
              placeholder="Voeg easy een actie toe..."
              className="mr-4 py-2 px-5 md:py-3 md:px-6 w-full wa-text-gray border-b h-14 "
            />
            <button
              type="submit"
              className="w-full md:w-auto mt-3 sm:mt-0 py-2 px-5 md:py-3 md:px-6 bg-green-600 hover:bg-green-800 font-bold text-white md:text-lg rounded-full shadow-md"
              id="quick-add-button"
            >
              Toevoegen
            </button>
          </form>
        </div>
      </div>
    </div>
  );
}

export default ActielijstSnelToevoegen;
