import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

class DashboardCardCijfer extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div
        className={`w-full mb-6 md:mb-0 md:w-1/4 ${
          this.props.marginRight ? 'mr-6' : ''
        }`}
      >
        <div className="bg-white h-full overflow-hidden p-5">
          <div className="flex justify-between h-full items-center flex-col flex-wrap py-6">
            <span className="font-bold text-center text-gray-800 text-2xl">
              {this.props.title}
            </span>

            <span className="text-6xl text-gray-800 font-bold leading-none">
              {this.props.cijfer}
            </span>

            {this.props.smiley ? (
              <span className="text-6xl mt-5 text-gray-800 font-bold leading-none">
                {this.props.smiley}
              </span>
            ) : null}

            <Link
              to="/mijn-werkleven/2/3"
              className="py-2 px-5 mt-6 w-40 text-center bg-wa-button-bordeaux hover:bg-green-800 text-white md:text-sm rounded-full cursor-pointer shadow-md"
            >
              {this.props.tekst}
            </Link>
          </div>
        </div>
      </div>
    );
  }
}

DashboardCardCijfer.propTypes = {
  title: PropTypes.string,
  cijfer: PropTypes.string,
};

export default DashboardCardCijfer;
