import React from 'react';

function Checkbox(props) {
  if (
    props.initialCheckboxValuesArray &&
    props.initialCheckboxValuesArray.includes(props.checkBoxValue)
  ) {
    return (
      <span className=" cursor-pointer  py-1 ">
        <label className="text-gray-700 si si-checkbox">
          <input
            defaultChecked
            className="mr-2 leading-tight"
            type="checkbox"
            value={props.checkBoxValue}
            onClick={e => {
              props.handleChecked(e, props.checkBoxValue);
            }}
          />
          <span className="text-xs md:text-base select-none si-label">
            {props.checkBoxValue}
          </span>
        </label>
      </span>
    );
  } else {
    return (
      <span className=" cursor-pointer  py-1 ">
        <label className="text-gray-700 si si-checkbox">
          <input
            className="mr-2 leading-tight"
            type="checkbox"
            value={props.checkBoxValue}
            onClick={e => {
              props.handleChecked(e, props.checkBoxValue);
            }}
          />
          <span className="text-xs md:text-base select-none si-label">
            {props.checkBoxValue}
          </span>
        </label>
      </span>
    );
  }
}

export default Checkbox;
