import React, { Component } from 'react';
import PropTypes from 'prop-types';

function SmallGreenLabel(props) {
  return (
      <li className="w-30 px-6 py-4 wg-dark-green-bg text-sm rounded-md inline mr-6 flex justify-start items-center text-center">
          <span className="align-top block">{props.children}</span>
      </li>
  );
}

function DataPlaceholder(props) {
  return (
      <span className="italic font-sm text-gray-700 w-full text-center">
          {props.children}
      </span>
  );
}

class DashboardDoelenSlider extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
        <ul className="mt-6 flex overflow-x-scroll pb-6 ml-4">
            {this.props.goals ? ( this.props.goals.map((item, index) => {
              return (
                  <SmallGreenLabel key={index}>{item[0]?.labelNaam}</SmallGreenLabel>
              );
          })
        ) : (
            <DataPlaceholder>Hier komen je doelen</DataPlaceholder>
        )}
        </ul>
    );
  }
}

DashboardDoelenSlider.propTypes = {};

DashboardDoelenSlider.defaultProps = {};

export default DashboardDoelenSlider;
