import React, { useEffect, useState } from 'react';
import { AlertTriangle, X } from 'react-feather';
import PopUpContainer from '../PopUpContainer';
import Trash from '../Atoms/Trash-Button';
import EditInputField from '../Molecules/Edit-Input-field';
import DeadLinePicker from '../Organisms/DeadLine-Picker';
import AddActionBar from '../Molecules/Add-Action-Bar';
import ActionToolsBar from '../Organisms/ActionToolsBar';
import PrimaryActionButton from '../Atoms/PrimaryAction-Button';
import TitleInput from '../Atoms/Title-Input';
import ActionDoubtsTools from '../Organisms/Action-Doubts-Tools';
import ActionSelectGoals from '../Organisms/Action-Select-Goals';
import { getAllGoals } from '../../functions/goalsHelper';
import {
  createActionObj,
  updateActionObj,
  updateActionTMP,
  updateActionToFB,
} from '../../functions/actionsHelper';
import { toast } from 'react-toastify';

const PopUpActionsEdit = props => {
  const [action, setAction] = useState({ ...props.action });
  const [toggleDoubts, setToggleDoubts] = useState(false);
  const [popupGoalSettings, setPopupGoalSettings] = useState(false);

  const [actionOldTitle, setActionOldTitle] = useState();
  const [actionTitle, setActionTitle] = useState(
    props.action.actieNaam,
  );
  const [actionDeadline, setActionDeadline] = useState(
    props.action.deadline,
  );
  const [actionNote, setActionNote] = useState(props.action.notes);

  const [actionHowAction, setActionHowAction] = useState(
    props.action.hoeForm.hoeActie,
  );
  const [actionHowOthers, setActionHowOthers] = useState(
    props.action.hoeForm.hoeAndere,
  );
  const [actionHowNeed, setActionHowNeed] = useState(
    props.action.hoeForm.hoeNodig,
  );
  const [actionHowSaved, setActionHowSaved] = useState(
    props.action.hoeForm.savedState,
  );

  useEffect(() => {
    props.setSelectedGoal(props.action);
    setAction(props.action);
  }, [props.selectedAction]);

  const handleChange = e => {
    e.preventDefault();
    let value = e.target.value;
  };

  const setAnActionDeadline = e => {
    setActionDeadline(e.target.value);
  };

  const newValue = e => {
    const name = e.target.name;
    const value = e.target.value;
    setAction(inputs => ({ ...inputs, [name]: value }));
  };

  const howValues = e => {
    const name = e.target.name;
    const value = e.target.value;
    let hoeForm = {
      [name]: [value],
    };
    setAction(inputs => ({ ...inputs, [name]: value }));
  };

  const setAddNotes = notes => {
    setActionNote(notes);
    setAction(inputs => ({ ...inputs, ['notes']: notes }));
  };

  const toggleDoubt = () => {
    console.log('CLOSE POPUP');
    props.togglePopUp();
    setPopupGoalSettings(prev => !prev);
    setToggleDoubts(prev => !prev);
    props.doubtFlow();
  };

  const selectedNewDeadline = newSelectedDate => {
    setActionDeadline(newSelectedDate);
  };

  const setNewActionTitle = e => {
    setActionTitle(e.target.value);
  };

  const updateAction = () => {
    let updatedAction = {};
    let actionTmp = {};
    //new created actions have an aUUID old ones not.

    if (action.aUUID) {
      actionTmp.hoeForm = {
        openState: false,
        savedState: actionHowSaved,
        hoeActie: actionHowAction,
        hoeAndere: actionHowOthers,
        hoeNodig: actionHowNeed,
      };

      updatedAction = updateActionObj(
        action,
        props.goal.UUID,
        actionTitle,
        actionDeadline,
        actionNote,
        actionTmp.hoeForm,
        action.toggleVoltooid,
      );
      updateActionTMP(
        props.firebase,
        props.authUser,
        updatedAction,
      ).then(result => {
        //setAction(result);
        props.setLoading(true);
        if (props.updatedAction) {
          props.updatedAction(result);
        }
        toast('Actie is gewijzigd! 👍');
      });
    } else {
      //Old actions need to be updated with propterties
      updatedAction = createActionObj(
        actionTitle,
        props.action.UUID,
        actionDeadline,
        action.notes,
      );
      updatedAction.hoeForm = {
        openState: false,
        savedState: actionHowSaved,
        hoeActie: actionHowAction,
        hoeAndere: actionHowOthers,
        hoeNodig: actionHowNeed,
      };
      updateActionToFB(
        props.firebase,
        props.authUser,
        updatedAction,
        props.goal,
        actionOldTitle,
      ).then(() => {
        props.setLoading(true);
      });
    }
  };

  return (
    <>
      <div className="w-screen h-screen z-40 left-0 top-0 bg-black opacity-25 fixed" />
      <div className="flex z-50 justify-center items-center w-screen h-screen top-0 left-0 fixed">
        <div className="bg-white w-full py-6 px-2 md:w-2/3 m-2  overflow-y-auto max-h-full rounded shadow-lg md:p-8 relative">
          <div>
            <X
              className="absolute right-0 top-0 mr-2 mt-2 cursor-pointer"
              onClick={() => {
                props.togglePopUp();
              }}
            />
          </div>
          <div className="w-full">
            <div className="flex">
              <div>
                <EditInputField
                  currentTitle={actionTitle}
                  handleOnChange={setNewActionTitle}
                  edit={updateAction}
                  name={'actieNaam'}
                />
              </div>
            </div>
            <div className="mb-6 border-b-2 border-gray-300 border-dashed w-full flex justify-around items-center"></div>
          </div>

          <ActionDoubtsTools
            action={props.action}
            updateAction={updateAction}
            handleChange={handleChange}
            setToggleDoubts={toggleDoubt}
            toggleDoubts={toggleDoubts}
            togglePopUp={props.togglePopUp}
            setHow={newValue}
            actionHow={action}
            isOpen={props.isOpen}
            actionHowAction={actionHowAction}
            setActionHowAction={setActionHowAction}
            actionHowOthers={actionHowOthers}
            setActionHowOthers={setActionHowOthers}
            actionHowNeed={actionHowNeed}
            setActionHowNeed={setActionHowNeed}
            actionHowSaved={actionHowSaved}
            setActionHowSaved={setActionHowSaved}
          />
          <ActionSelectGoals
            goals={props.goals}
            goal={props.goal}
            item={props.action}
            handleLabelChange={props.addNewGoalFromAction}
            setActionDeadline={setAnActionDeadline}
            actionDeadline={actionDeadline}
            actionNotes={action.notes}
            setActionNotes={setAddNotes}
            update={updateAction}
          />
          <div className="flex justify-end mt-2">
            <PrimaryActionButton handleClick={updateAction}>
              Opslaan
            </PrimaryActionButton>
          </div>
        </div>
      </div>
    </>
  );
};

export default PopUpActionsEdit;
