import React, { Component } from 'react';
import {
  SortableContainer,
  SortableElement,
} from 'react-sortable-hoc';
import { Menu, Edit, Trash2 } from 'react-feather';
import SortableTodoItem from "../Organisms/Sortable-Todo-Item";

const SortableItem = SortableElement(
  ({
    item,
    index,
    listPosition,
    removeItem,
    editTitleItem,
    toggleEditItem,
  }) => (
      <SortableTodoItem
          item={item}
          listPosition={listPosition}
          editTitleItem={editTitleItem}
          toggleEditItem={toggleEditItem}
          removeItem={removeItem}
      />
  ),
);

class SortableComponent extends Component {
  render() {
    return (
      <SortableList
        items={this.props.arrayItems}
        toggleEditItem={this.props.toggleEditItem}
        editTitleItem={this.props.editTitleItem}
        removeItem={this.props.removeItem}
        onSortEnd={this.props.onSortEnd}
        distance={5}
      />
    );
  }
}

const SortableList = SortableContainer(
  ({ items, removeItem, editTitleItem, toggleEditItem }) => {
    return (
      <ol>
        {items !== undefined
          ? items.map((value, index) => {
              console.log(index);
              return (
                <SortableItem
                  listPosition={index}
                  key={`item-${index}`}
                  index={index}
                  item={value}
                  removeItem={removeItem}
                  editTitleItem={editTitleItem}
                  toggleEditItem={toggleEditItem}
                />
              );
            })
          : null}
      </ol>
    );
  },
);

class TodoForm extends React.Component {
  constructor(props) {
    super(props);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentDidMount() {
    // this.refs.itemName.focus();
  }

  onSubmit(event) {
    event.preventDefault();
    var newItemValue = this.refs.itemName.value;

    if (newItemValue) {
      this.props.addItem({ newItemValue });
      this.refs.form.reset();
    }
  }
  render() {
    return (
      <form
        ref="form"
        onSubmit={this.onSubmit}
        className="text-gray-700 py-2 mb-2 mt-4 relative flex"
      >
        <input
          type="text"
          autoComplete="off"
          ref="itemName"
          className="mr-4 border-dashed border-b w-full h-14 pl-4 pr-4"
          placeholder="Voeg punt toe"
        />
        <button
          type="submit"
          className="w-32 border-2 wg-dark-green-bg hover:bg-blue-600 hover:text-white px-5 py-1 rounded-full inline-block font-bold text-blue-600 whitespace-no-wrap shadow"
        >
          Voeg toe
        </button>
      </form>
    );
  }
}

class TodoApp extends React.Component {
  render() {
    const arrayItems = this.props.arrayItems;
    const removeItem = this.props.removeItem;
    const addItem = this.props.addItem;
    const onSortEnd = this.props.onSortEnd;
    const editTitleItem = this.props.editTitleItem;
    const toggleEditItem = this.props.toggleEditItem;

    return (
      <div id="main">
        <SortableComponent
          onSortEnd={onSortEnd}
          arrayItems={arrayItems}
          removeItem={removeItem}
          editTitleItem={editTitleItem}
          toggleEditItem={toggleEditItem}
        />
        <TodoForm addItem={addItem} />
      </div>
    );
  }
}

export default TodoApp;
