import React from 'react';


import {
  ChevronDown,
  ChevronUp,
  Plus,
  X,
  Tag,
  Calendar,
  AlertTriangle,
} from 'react-feather';

import Lightbulb from './../Lightbulb';
import SelectLabel from './../SelectLabel';
import ActielijstNotitie from './../ActielijstNotitie';
import ActielijstHoeForm from './../ActielijstHoeForm';
import {
  updateActionTMP,
  updateActionObj,
} from '../../functions/actionsHelper';
import { withAuthentication } from '../Session';
import { withFirebase } from '../Firebase';
import PrimaryActionButton from '../Atoms/PrimaryAction-Button';
import { toast } from 'react-toastify';
import Tooltip from '../Atoms/Tooltip';

class ActielijstItemPopup extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hoeFormOpen: false,
      deadline: null,
      title: props.item.actieNaam,
      buttonDeadline: props.item.deadline
        ? 'Deadline'
        : 'Voeg een deadline toe',
    };
    this.toggleHoeForm = this.toggleHoeForm.bind(this);
    this.selectedADate = this.selectedADate.bind(this);
    this.save = this.save.bind(this);
    this.saveDate = this.saveDate.bind(this);
  }

  toggleHoeForm() {
    this.setState(
      {
        hoeFormOpen: !this.state.hoeFormOpen,
      },
      () => console.log(this.state.hoeFormOpen),
    );
  }

  saveDate(e) {
    toast('Datum gewijzigd! 👍');
  }

  onChangeDate(e) {
    this.setState({
      deadline: e.target.value,
      buttonDeadline: 'Wijzig de deadline',
    });
  }

  selectedADate(e) {
    e.preventDefault();
    let action = this.props.item;

    this.setState({
      deadline: e.target.value,
    });

    action.deadline = this.state.deadline || action.deadline;

    let updatedActionObj;
    updatedActionObj = updateActionObj(action);

    //this is an action with a goal
    if (action.UUID) {
      updateActionTMP(
        this.props.firebase,
        this.props.authUser,
        updatedActionObj,
      ).then(() => {
        //this.props.setLoading(true);
      });
      this.props.updateThought(action, false);
    } else {
      action.deadline = this.state.deadline;
      //this.props.submitNewDateToThought(e, action);

      this.props.updateThought(action, true);
    }
  }

  save(e) {
    e.preventDefault();
    let action = this.props.item;

    let updatedActionObj;
    //this is an action with a goal
    if (action.UUID) {
      action.deadline = this.state.deadline || action.deadline;
      updatedActionObj = updateActionObj(action);
      updateActionTMP(
        this.props.firebase,
        this.props.authUser,
        updatedActionObj,
      ).then(() => {
        //this.props.setLoading(true);
        this.props.updateThought(action, false);
        toast('Actie gewijzigd! 👍');
      });
    } else {
      action.deadline = this.state.deadline || action.deadline;
      //this.props.submitNewDateToThought(e, action);
      toast('Actie gewijzigd! 👍');
      this.props.updateThought(action, true);
    }
  }

  render() {
    const item = this.props.item;
    const index = this.props.index;
    const titleDeadline = this.state.buttonDeadline;

    return (
      <React.Fragment>
        <div className="w-screen h-screen z-40 left-0 top-0 bg-black opacity-25 fixed"></div>
        <div className="w-full h-screen pb-6 pt-20 fixed z-50 left-0 top-0 flex justify-center items-center mx-auto">
          <div
            id="item-module"
            className="bg-white w-2/3 overflow-y-auto max-h-full rounded shadow-lg p-8 relative"
          >
            <div className="absolute right-0 top-0 mr-5 mt-5">
              <X
                className="inline-block ml-5 cursor-pointer"
                onClick={e => {
                  this.save(e);
                  this.props.toggleOpenState();
                }}
              />
            </div>

            <form
              className="w-full inline-block"
              data-index={index}
              data-name="deadline"
              onSubmit={this.props.submitNieuwTitle}
              data-name="actieNaam"
            >
              <input
                value={this.props.actionTitle || item.actieNaam}
                onChange={e => {
                  this.props.handleChange(e);
                }}
                data-index={index}
                name="actieNaam"
                className="w-10/12 font-bold text-gray-800 text-2xl mb-4 border border-gray-300"
              />
              <button
                type="submit"
                className="mt-1 cursor-pointer bg-green-600 hover:bg-green-800 text-white rounded-r px-5 py-1 font-bold"
              >
                Wijzig
              </button>
            </form>

            {/* Divider Line */}
            <div className="h-1 block w-full border-dashed mt-6 mt-2 border-b-2 border-gray-300"></div>

            <div className="mb-6 border-b-2 border-gray-300 border-dashed w-full flex justify-around items-center">
              <div className="inline-block flex justify-center items-middle">
                <div
                  className="my-4 cursor-pointer hover:bg-blue-500 hover:text-white rounded px-5 py-1 border-blue-500 border font-bold text-blue-500"
                  data-index={index}
                  data-name="hoeForm"
                  onClick={this.toggleHoeForm}
                >
                  {this.state.hoeFormOpen ? (
                    <ChevronUp className="pointer-events-none inline-block relative -mt-1 mr-1" />
                  ) : (
                    <ChevronDown className="pointer-events-none inline-block relative -mt-1 mr-1" />
                  )}
                  Hoe
                </div>
              </div>
              <div className="inline-block flex justify-center items-middle">
                <span
                  className="my-4 cursor-pointer hover:bg-blue-500 hover:text-white rounded px-5 py-1 border-blue-500 border font-bold text-blue-500"
                  data-index={index}
                  data-name="twijfelForm"
                  onClick={() => {
                    this.props.toggleTwijfelFlow();
                    this.props.toggleOpenState();
                  }}
                >
                  <Plus className="inline-block relative -mt-1 mr-1" />
                  Ik Twijfel
                </span>
              </div>
            </div>
            {this.state.hoeFormOpen ? (
              <ActielijstHoeForm
                item={item}
                index={index}
                handleHoeOfTwijfelChange={
                  this.props.handleHoeOfTwijfelChange
                }
                submitHoeForm={this.props.submitHoeForm}
              />
            ) : null}

            <div className="w-full">
              <div className="flex">
                <div>
                  <span className="text-gray-700 font-bold font-lg block w-40">
                    <Tag
                      className="inline-block mr-2 relative text-gray-700 font-bold -mt-1 relative"
                      size={25}
                    />
                    Doelen
                    <Tooltip>Kies of maak een doel waar deze actie onder valt.</Tooltip>
                  </span>

                  <span className="mt-2 inline-block w-64">
                    <SelectLabel
                      labels={this.props.labels}
                      fromActielijst={true}
                      standardLabel={item.labels}
                      index={index}
                      item={item}
                      handleLabelChange={this.props.handleLabelChange}
                    />
                  </span>
                </div>
              </div>
              <div>
                <span className="mt-6 text-gray-700 font-bold font-lg inline-block w-32">
                  <Calendar
                    className="inline-block mr-2 relative text-gray-700 font-bold -mt-1 relative"
                    size={25}
                  />
                  Deadline
                </span>
                <input
                  type="date"
                  onChange={e => {
                    this.onChangeDate(e);
                  }}
                  defaultValue={
                    this.props.actionDeadline || item.deadline
                  }
                  data-index={index}
                  name="deadline"
                  required
                  min="2019-01-01"
                  className="datum-input-actielijst-item border-r-0 pl-2 bg-gray-100 border text-lg rounded-l"
                />
                <button
                  type="button"
                  className="mt-1 cursor-pointer bg-green-600 hover:bg-green-800 text-white rounded-r px-5 py-1 font-bold"
                  onClick={e => {
                    this.saveDate(e);
                  }}
                >
                  {titleDeadline}
                </button>
                <span>
                  <Tooltip
                    content={
                      <div className="max-w-sm p-4 pl-6">
                        <div>
                          Kies de datum waarop jij deze actie/doel
                          behaalt wilt hebben. De datum kan je altijd
                          nog veranderen.
                        </div>
                        <br />
                        <div>
                          Handigheidje: WorkAppic herinnert jou aan
                          het uitvoeren van de acties en doelen door
                          het sturen van e-mailnotificaties.
                        </div>
                      </div>
                    }
                    className="inline-block z-20"
                  >
                    <Lightbulb />
                  </Tooltip>
                </span>
              </div>

              {!this.state.deadline &&
              this.props.action &&
              !this.props.action.deadline ? (
                <div className="mt-5">
                  <AlertTriangle className="inline-block mr-2 relative text-orange-600 icon-position" />
                  <span>Datum moet nog worden toegevoegd</span>
                </div>
              ) : null}
            </div>

            {/* Divider Line */}
            <div className="h-1 block w-full border-dashed mt-6 mt-2 border-b-2 border-gray-300"></div>

            <div className="mt-6">
              <h3 className="text-gray-700 font-bold font-lg">
                Nieuwe Notitie
              </h3>
              <form
                className="mt-1"
                onSubmit={e => {
                  this.props.voegNieuweNotitieToe(e, index);
                }}
              >
                <textarea
                  placeholder="Type hier je notitie..."
                  name="nieuweNotitie"
                  value={this.props.actionNotes || item.nieuweNotitie}
                  data-index={index}
                  onChange={e => {
                    this.props.handleChange(e);
                  }}
                  // onChange={(e) => {this.props.handleChange(e, this.props.index)}}
                  className="border rounded w-full p-4"
                />
                <button className="mt-1 cursor-pointer bg-green-600 hover:bg-green-800 text-white rounded px-5 py-1 font-bold">
                  Notitie toevoegen
                </button>
              </form>
            </div>

            {/* <div className="h-1 block w-full border-dashed mt-6 mt-2 border-b-2 border-gray-300"></div> */}

            <div className="flex flex-row mt-6">
              {item.notes
                ? item.notes
                    .slice(0)
                    .reverse()
                    .map((notes, index) => {
                      return (
                        <ActielijstNotitie
                          key={index}
                          notitieInhoud={notes.content}
                          notitieDatum={notes.date}
                        />
                      );
                    })
                : null}
            </div>
            <div className="flex justify-end mt-2">
              <PrimaryActionButton
                handleClick={e => {
                  this.save(e);
                }}
              >
                Opslaan
              </PrimaryActionButton>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default withAuthentication(withFirebase(ActielijstItemPopup));
