import React, { Component } from 'react';

import { Lock } from 'react-feather';

function DashboardModuleListItem(props) {
  return (
    <li
      onClick={() => {
        alert(
          'We werken momenteel hard aan de ontwikkeling van de modulen. Deze komen eraan! 😄',
        );
      }}
      className="py-2 mx-3 my-2 px-5 mt-6 text-center bg-green-600 hover:bg-green-800 font-bold text-white md:text-lg rounded-lg cursor-pointer shadow-md"
    >
      <span className="mx-5 py-2 block">
        <Lock className="inline-block mr-2 relative text-white icon-position" />
        {props.name}
      </span>
    </li>
  );
}

export default DashboardModuleListItem;
