import React from 'react';

const ButtonSmallGreen = (props) => {
    return (
        <button
            onClick={props.onClick}
            className="w-56 h-10 text-sm cursor-pointer bg-wa-button-green hover:bg-green-800 text-white rounded-full px-5 py-1"
        >
            {props.title}
        </button>
    );
};

export default ButtonSmallGreen;
