import React, { useState, useEffect } from 'react';
import { List } from 'react-feather';
import LoaderActielijst from '../Loaders/LoaderActielijst';
import isBefore from 'date-fns/is_before';
import addDays from 'date-fns/add_days';
import isAfter from 'date-fns/is_after';
import Deadline from '../Atoms/Deadline';
import CheckedCircle from '../Atoms/CheckedCircle';
import { makeDateObj } from '../../functions/utils';

const ActionListContainer = props => {
  const [weekAction, setWeekAction] = useState([]);

  const startOfWeek = date => {
    var diff =
      date.getDate() - date.getDay() + (date.getDay() === 0 ? -6 : 1);
    return new Date(date.setDate(diff)).setHours(0, 0, 0, 0);
  };

  useEffect(() => {
    if (props.actions) {
      const newArray = props.actions?.filter((element, index) => {
        if (
          isBefore(element.deadline, addDays(new Date(), 8)) &&
          isAfter(
            makeDateObj(element.deadline),
            startOfWeek(new Date()),
          )
        ) {
          return element;
        }
        return null;
      });
      setWeekAction(newArray);
    }
  }, [props.actions]);

  return (
    <div className="w-full overflow-hidden">
      <h1 className="font-bold text-xl md:text-2xl text-gray-700 mb-4">
        <List className="inline-block mr-2 relative text-green-600 icon-position" />
        Deze week
      </h1>
      <div className="md:ml-10 flex flex-col align-middle">
        <ul>
          {weekAction && weekAction.length > 0 ? (
            weekAction.map((action, index) => {
              // Set original index here
              return (
                <li
                  className="flex flex-column items-center border-b"
                  key={index}
                >
                  <CheckedCircle
                    size={15}
                    voltooid={action.voltooid}
                    toggleVoltooid={() => {}}
                  />
                  <h3
                    className={`font-bold text-lg text-gray-700 flex-grow  ${
                      action.voltooid ? 'line-through' : null
                    }`}
                  >
                    {action.actieNaam}
                  </h3>
                  <Deadline
                    date={action.deadline}
                    state={action.voltooid}
                  />
                </li>
              );
            })
          ) : (
            <div>
              <h3 className="font-bold text-lg text-gray-700 flex-grow">
                Je hebt geen acties voor komende week!
              </h3>
            </div>
          )}
        </ul>
      </div>
    </div>
  );
};

export default ActionListContainer;
