import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Lightbulb from './../../components/Lightbulb';
import Tooltip from 'react-tooltip-lite';

class DashboardQuickAddActielijst extends Component {
  constructor(props) {
    super(props);
  }
  null;
  render() {
    return (
      <div className="w-full md:w-2/3 mb-24" id="quick-add-actie">
        <div className="bg-white shadow-xl rounded-lg overflow-hidden md:flex">
          <div className="w-full p-4 pl-2 md:p-5 md:pl-3">
            <form
              className="flex-row md:flex block"
              onSubmit={this.props.voegNieuweActieToe}
            >
              <div className="flex w-full">
                <span className="relative mt-4 mr-2">
                  <Tooltip
                    content={
                      <div className="max-w-sm">
                        <p>
                          Gedachten zijn ideeën, acties, vragen en
                          twijfels. Voeg hier gedachten toe en ze
                          staan direct in de mindbox van jouw
                          actielijst om te verwerken.
                        </p>
                      </div>
                    }
                    className="inline-block z-20"
                  >
                    <Lightbulb />
                  </Tooltip>
                </span>
                <input
                  placeholder="Voeg hier snel een gedachte toe..."
                  className="mr-4 border-b w-full h-14 px-4"
                  name="nieuweActie"
                />
              </div>
              <button
                className="mt-3 sm:mt-0 py-2 px-5 md:py-3 md:px-6 bg-green-600 hover:bg-green-800 font-bold text-white md:text-lg rounded-lg shadow-md w-full md:w-auto"
                id="quick-add-button"
              >
                Toevoegen
              </button>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

DashboardQuickAddActielijst.propTypes = {
  voegNieuweActieToe: PropTypes.func.isRequired,
};

export default DashboardQuickAddActielijst;
