export const day = givenDate => {
  let destructedDate = 0;
  if (givenDate) {
    destructedDate = givenDate.split('-');
  }
  return parseInt(destructedDate[2]);
};

const yearToNumber = givenDate => {
  let destructedDate = 0;
  if (givenDate) {
    destructedDate = givenDate.split('-');
  }
  const total =
    destructedDate[0] +
    '' +
    destructedDate[1] +
    '' +
    destructedDate[2];
  return parseInt(total);
};

export const sortActions = actions => {
  if (actions.length > 1) {
    actions.sort((a, b) =>
      yearToNumber(a.deadline) > yearToNumber(b.deadline) ? 1 : -1,
    );
  }
  return actions;
};

export const sortByDay = setOfMonths => {
  if (setOfMonths.length > 1) {
    setOfMonths.sort((a, b) =>
      day(a.deadline) > day(b.deadline) ? 1 : -1,
    );
  }

  return setOfMonths;
};

export const checkNextMonth = (
  goals,
  monthNumber,
  currentRowPosition,
  monthCount,
  zoomedIn,
  selectedMonthNumber,
) => {
  let totalCollection = [];
  let collectedByMonth = [];

  for (let i = 0; i < goals.length; i++) {
    if (zoomedIn) {
      if (goals[i].rowPosition === selectedMonthNumber) {
        collectedByMonth.push(goals[i]);
      }
    } else {
      if (
        goals[i].rowPosition === monthNumber &&
        monthNumber <= monthCount
      ) {
        //console.log(goals[i].labelNaam)
        collectedByMonth.push(goals[i]);
      }
    }
  }
  let sortedGoals = sortByDay(collectedByMonth);

  return sortedGoals;
};

export const checkGoalsOnMonth = (
  goals,
  monthCount,
  zoomedIn,
  selectedMonthNumber,
) => {
  let allMonths = [];
  let currentRowPosition = 0;
  let prevRowPosition = 0;

  if (goals.length > 0) {
    for (let i = 0; i < goals.length; i++) {
      let collectedMonths = [];
      if (prevRowPosition != goals[i].rowPosition) {
        collectedMonths = checkNextMonth(
          goals,
          goals[i].rowPosition,
          currentRowPosition,
          monthCount,
          zoomedIn,
          selectedMonthNumber,
        );
      }
      prevRowPosition = goals[i].rowPosition;
      if (collectedMonths.length > 0) {
        allMonths.push(collectedMonths);
      }
    }
  }
  /*if (allMonths.length >= 1){
      sortByDay(allMonths)
    }*/

  return allMonths;
};
