import React from 'react';
import Tooltip from 'react-tooltip-lite';

import RadioButton from './../../../../components/RadioButton';
import Slider from './../../../../components/Slider';
import Editor from './../../../../components/Editor';
import Lightbulb from './../../../../components/Lightbulb';
import LoaderEditor from './../../../../components/Loaders/LoaderEditor';
import LoaderCheckboxList from './../../../../components/Loaders/LoaderCheckboxList';
import LoaderSlider from './../../../../components/Loaders/LoaderSlider';
import TitleQuestionair from "../../../../components/Atoms/Title-Questionair";

function MijnWerkleven2Pagina1(props) {
  return (
    <React.Fragment>
      <TitleQuestionair>
        A. Huidig werkleven{' '}
      </TitleQuestionair>

      <h3 className="font-bold text-gray-700 mb-2">
        Welk cijfer geef ik mijn huidige werkleven?
      </h3>
      {props.loading === false ? (
        <Slider
          sliderValue={props.HWSliderValue}
          onChange={props.handleSliderChange}
          keyName="HWCijfer"
        />
      ) : (
        <LoaderSlider />
      )}
      <div className="mt-5">
        <h3 className="font-bold text-gray-700 mb-2">
          Waarom geef ik dat cijfer?
        </h3>
        {props.loading === false ? (
          <Editor
            setEditorState={props.setEditorState}
            fieldName="HWToelichting"
            initialEditorState={props.initialEditorStateHWToelichting}
          />
        ) : (
          <LoaderEditor />
        )}
      </div>
      <div className="mt-5">
        <h3 className="font-bold text-gray-700 mb-2">
          Hoe voel ik mij bij dat cijfer? Kies een smiley.
        </h3>
        {props.loading === false ? (
          <form
            className="mt-6 text-2xl"
            onChange={e => props.handleChange(e)}
          >
            <RadioButton
              checked={props.HWSmiley === '1'}
              labelValue="😭"
              value="1"
              name="HWSmiley"
            />
            <RadioButton
              checked={props.HWSmiley === '2'}
              labelValue="😔"
              value="2"
              name="HWSmiley"
            />
            <RadioButton
              checked={props.HWSmiley === '3'}
              labelValue="😐"
              value="3"
              name="HWSmiley"
            />
            <RadioButton
              checked={props.HWSmiley === '4'}
              labelValue="🙂"
              value="4"
              name="HWSmiley"
            />
            <RadioButton
              checked={props.HWSmiley === '5'}
              labelValue="😁"
              value="5"
              name="HWSmiley"
            />
          </form>
        ) : (
          <LoaderCheckboxList />
        )}
      </div>
      {/* <div className="mt-8">
        <h3 className="font-bold text-gray-700 mb-2">
          Waarom kies ik die smiley?
        </h3>
        {props.loading === false ? (
          <Editor
            setEditorState={props.setEditorState}
            fieldName="HWGevoel"
            initialEditorState={props.initialEditorStateHWGevoel}
          />
        ) : (
          <LoaderEditor />
        )}
      </div> */}
    </React.Fragment>
  );
}

export default MijnWerkleven2Pagina1;
